.recommendation-card {
	padding: 15px 10px;
	background-color: #fbfbfb;
	border-radius: 15px;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	margin-bottom: 20px;
	&:last-child {
		margin-bottom: 0;
	}
	&__img {
		width: 126px;
		min-width: 126px;
		height: 90px;
		margin-right: 15px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		font-weight: bold;
		font-size: 12px;
		line-height: 14px;
		text-transform: uppercase;
		color: $black-text;
		margin-bottom: 13px;
	}
	&__price {
		display: flex;
		align-items: center;
		// flex-wrap: wrap;
		justify-content: flex-start;
		margin-bottom: 18px;
	}
	&__price-uah {
		font-weight: 600;
		font-size: 12px;
		line-height: 14px;
		color: $red;
	}
	&__price-usd {
		font-weight: 700;
		font-size: 16px;
		line-height: 1;
		color: $red;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		&::before {
			content: '';
			margin: 0 6px;
			width: 1px;
			display: inline-block;
			height: 16px;
			background-color: $red;
		}
	}
	&__details {
		font-family: $secondary-font;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-decoration: none;
		font-weight: 500;
		font-size: 12px;
		line-height: 20px;
		/* identical to box height, or 167% */
		text-align: right;
		text-transform: uppercase;
		color: $black-text;
		svg {
			width: 20px;
			height: 13px;
			margin-left: 15px;
			transition: 0.3s all;
		}

		&:hover {
			svg {
				transform: translateX(5px);
			}
		}
	}

	@media (min-width: 1024px) and (max-width: 1400px) {
		flex-direction: column;
		max-width: 200px;
		width: 100%;
		// margin-left: auto;
		margin-right: auto;
		&__img {
			width: 100%;
			max-width: 190px;
			height: auto;
		}
		&__text {
			margin-top: 10px;
		}
		&__price {
			margin-bottom: 10px;
		}
	}
}
