.category-card {
	min-height: 230px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	text-decoration: none;
	margin-bottom: 30px;
	position: relative;
	overflow: hidden;
	z-index: 1;
	background-color: $black;
	&__img {
		width: fit-content;
		display: flex;
		align-items: center;
		justify-content: center;
		pointer-events: none;
		&_absolute-left {
			position: absolute;
			left: 0;
		}
		&_absolute-right {
			position: absolute;
			right: 0;
		}
		&_absolute {
			position: absolute;
			top: -10px;
		}
		.error {
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
		}
	}
	&__title {
		@extend %category-title-1;
		position: relative;
		z-index: 2;
		transition: 0.3s all;
		-webkit-text-fill-color: transparent;
		-webkit-text-stroke: 1.5px #ffffff;
		&_3 {
			padding-left: 211px;
			text-align: right;
		}
		&_4 {
			padding-bottom: 20px;
		}
		&_5 {
			padding-right: 220px;
		}

		&_small {
			@extend %category-title-2;
		}
		&_left {
			text-align: left;
		}
	}
	&__wrapper {
		.btn-primary {
			opacity: 0;
			visibility: hidden;
			padding: 0;
		}
		.btn-right {
			margin-left: auto;
		}
		.btn-center {
			margin-right: auto;
			margin-left: auto;
		}
		&_left {
			padding-left: 20px;
		}
		&_right {
			padding-right: 20px;
		}
	}
	&_column {
		flex-direction: column;
		justify-content: flex-end;
		padding-bottom: 20px;
	}
	&_between {
		justify-content: space-between;
	}
	&_right {
		justify-content: flex-end;
	}
	&_left {
		justify-content: flex-start;
	}
	.btn-primary {
		position: relative;
		z-index: 2;
	}
	@include _mobile {
		min-height: 150px;
		&__img {
			position: absolute;
			img {
				max-width: 60%;
			}
			&_absolute-left {
				justify-content: flex-start;
				align-items: flex-start;
			}

			&_right-2 {
				justify-content: flex-end;
				align-items: flex-end;
			}
			&_left {
				align-items: flex-start;
				justify-content: flex-start;
				left: 0;
			}
			&_right-mobile {
				left: auto;
				right: 0;
				width: 100%;
				justify-content: flex-end;
			}
		}
		//
		&__wrapper {
			&_right-2 {
				margin-left: auto;
				margin-right: 0;
			}
			&_left {
				margin-left: 0;
				margin-right: auto;
			}
			&_right {
				margin-left: auto;
				margin-right: 0;
			}
		}
		//titles
		&__title {
			&_1 {
				padding-right: 20px;
				padding-left: 0;
			}
			&_3 {
				padding-left: 0;
				padding-right: 20px;
				text-align: right;
			}
			&_4 {
				padding-bottom: 5px;
			}
			&_5 {
				padding-left: 20px;
				padding-right: 0;
				padding-bottom: 5px;
				text-align: left;
			}
		}
		.btn-primary {
			opacity: 1;
			visibility: visible;
			margin-top: 5px;
		}
	}
	@include _tablet {
		&__img {
			position: absolute;
			img {
				max-width: 70%;
			}
			&_absolute-left {
				justify-content: flex-start;
				align-items: flex-start;
			}
			&_right-2 {
				align-items: flex-end;
				justify-content: flex-end;
				// margin-left: auto;
				right: 0;
			}
			&_left {
				justify-content: flex-start;
				align-items: flex-start;
				left: 0;
			}
			&_right-mobile {
				left: auto;
				right: 0;
				justify-content: flex-end;
			}
		}
		.btn-primary {
			opacity: 1;
			visibility: visible;
			padding: 10px 5px;
			margin-top: 5px;
		}
		&__title {
			&_1 {
				padding-left: 0;
			}
			&_3 {
				padding-left: 0;
				text-align: center;
			}
			&_5 {
				padding-right: 0;
				text-align: center;
			}
			&_6 {
				padding-left: 20px;
				text-align: center;
			}
		}
		&__wrapper_left {
			margin-right: auto;
		}
		&__wrapper_right {
			margin-left: auto;
		}
	}
	@include _laptop-sm {
		&__title {
			&_1 {
				padding-left: 0;
			}
		}
	}

	@media (min-width: 1024px) {
		&:hover {
			.btn-primary {
				opacity: 1;
				visibility: visible;
				padding: 15px 10px;
				margin-top: 10px;
				// height: 50px;
			}
		}
		&_column {
			&:hover {
				.category-card__title {
					-webkit-text-fill-color: rgba(255, 255, 255, 0.7);
					-webkit-text-stroke: 1.5px $red;
				}
			}
		}
	}
}
