.delivery-payment {
	margin-bottom: 120px;
	.section-title-4 {
		margin-bottom: 40px;
	}
	&__heading {
	}

	&__heading-icon {
		width: 100px;
		height: 100px;
		margin-right: 30px;
		svg {
			width: 100%;
			height: 100%;
		}
	}
	&__heading-wrapper {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 40px;
		.section-title-4 {
			margin-bottom: 0;
		}
	}
	p {
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__section {
		margin-bottom: 60px;
		&:last-child {
			margin-bottom: 120px;
		}
		.section-title-5 {
			margin-bottom: 25px;
		}
		p {
			font-weight: normal;
			font-size: 18px;
			line-height: 24px;
			/* or 133% */
			color: #000000;
			margin-bottom: 40px;
			a {
				color: $red;
			}
		}
		ul {
			@extend %ul-line-2;
			margin-bottom: 40px;
			li {
				margin-bottom: 10px;
			}
		}
		.no-margin {
			margin-bottom: 10px;
		}
	}

	&__notice {
		margin-bottom: 60px;
		font-weight: 600;
		font-size: 18px;
		line-height: 26px;
		/* or 144% */
		color: #000000;
	}
	.red {
		color: $red;
		text-transform: uppercase;
		margin-bottom: 60px;
	}
	.delivery {
		margin-top: 60px;
	}
	@include _mobile {
		margin-bottom: 50px;
		.section-title-4 {
			font-size: 26px;
			margin-bottom: 20px;
		}
		&__notice {
			margin-bottom: 20px;
		}
		&__heading-wrapper {
			margin-bottom: 20px;
			margin-top: 20px;
		}
		&__heading-icon {
			width: 50px;
			height: 50px;
		}
		&__section {
			margin-bottom: 20px;

			.section-title-5 {
				font-size: 20px;
				line-height: 1;
			}

			.red {
				margin-bottom: 30px;
			}
			ul {
				margin-bottom: 20px;
			}

			&_first,
			&_last {
				margin-bottom: 40px;
			}
		}

		p {
			font-size: 14px;
			line-height: 16px;
			margin-bottom: 20px;
		}
		.delivery {
			margin-top: 30px;
		}
	}
	@include _tablet {
		margin-bottom: 70px;
		&__heading-wrapper {
			justify-content: center;
			align-items: center;
		}
	}
}
