.stars {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 20px;
		height: 20px;
		margin-right: 5px;
		svg {
			width: 100%;
			height: 100%;
			color: $red;
		}
		// &_empty {
		// }
	}

	@include _mobile {
		&__item {
			width: 10px;
			height: 10px;
			margin-right: 3px;
		}
	}
}
