.hero {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	min-height: 700px;
	padding-top: 190px;
	position: relative;
	top: -134px;
	z-index: 1;
	overflow: hidden;
	.container {
		position: relative;
		z-index: 2;
	}
	&::after {
		content: '';
		width: 100%;
		height: 100%;
		background-color: rgba(44, 44, 44, 0.8);
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
	}
	&__title {
		margin-top: 0;
		margin-bottom: 30px;
		@extend %hero-title;

		&_stroke-text {
			-webkit-text-fill-color: transparent;
			-webkit-text-stroke: 3px $red;
			text-shadow: 0px 1px 16px rgba(184, 33, 51, 0.3);
		}
	}
	&__subtitle {
		@extend %hero-subtitle;
		margin-bottom: 50px;
		animation: fadeIn 0.8s 1s linear backwards;
	}
	.btn-primary {
		margin-bottom: 60px;

		&::after {
			animation: hoverBtnLoad 0.3s linear 4s forwards;
		}
	}
	&__video {
		position: absolute;
		z-index: -1;
		left: 0;
		pointer-events: none;
		overflow: hidden;
		top: -90px;
		video {
			width: 120%;
			height: auto !important;
			// height: 120%;
		}
	}

	@include _mobile-tablet {
		video {
			width: auto !important;
			height: 100% !important;
		}
	}
	@include _mobile {
		margin-bottom: -74px;
		padding-top: 160px;
		padding-bottom: 67px;
		&__title {
			text-align: center;
			margin-bottom: 20px;
		}
		&__subtitle {
			margin-bottom: 40px;
			text-align: center;
		}
		.btn-primary {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 50px;
		}
		&__video {
			height: 140%;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
	@include _tablet {
		padding-top: 130px;
		min-height: 530px;
		top: -100px;
		padding-bottom: 30px;
		margin-bottom: -64px;
		&__subtitle {
			margin-bottom: 30px;
		}
		.btn-primary {
			margin-bottom: 30px;
		}
	}
	@include _laptop-sm {
		padding-top: 140px;
		min-height: 510px;
		margin-bottom: -74px;
		&__video {
			top: 0;
			video {
				width: 150%;
				// width: 143%;
			}
		}

		&__subtitle {
			margin-bottom: 30px;
		}

		.btn-primary {
			margin-bottom: 30px;
		}
	}
	@include _laptop {
		min-height: 600px;
		padding-top: 150px;
		&__video {
			video {
				width: 150%;
			}
		}
		// &__video {
		// 	// top: -25px;
		// 	height: 130;
		// 	video {
		// 		height: 150%;
		// 	}
		// }
	}

	@include _large {
		&__video {
			video {
				width: 150%;
			}
		}
	}
}

.hero-advantages {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-right: 40px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__item-icon {
		width: 44px;
		min-width: 44px;
		height: 44px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		background-color: $red;
		margin-right: 20px;
		svg {
			width: 24px;
			height: 24px;
			color: #fff;
		}
	}
	&__item-title {
		@extend %card-title-1;
		color: #ffffff;
		margin-bottom: 5px;
	}
	&__item-text {
		@extend %card-text-1;
		color: #fff;
	}

	@include _mobile {
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		&__item {
			margin-bottom: 15px;
			&:last-child {
				margin-bottom: 0;
			}
			&-icon {
				width: 38px;
				min-width: 38px;
				height: 38px;
				svg {
					width: 20px;
					height: 20px;
				}
			}
		}
	}
	@include _tablet {
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		&__item {
			margin-right: 0;
			margin-bottom: 15px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	@include _laptop-sm {
		&__item {
			margin-right: 15px;
		}
	}
}

.ready .hero-advantages {
	&__item {
		&:nth-child(1) {
			animation: slideUp 0.4s linear 2.2s backwards;
		}
		&:nth-child(2) {
			animation: slideUp 0.4s linear 2.6s backwards;
		}
		&:nth-child(3) {
			animation: slideUp 0.4s linear 3s backwards;
		}
		&:nth-child(4) {
			animation: slideUp 0.4s linear 3.4s backwards;
		}
	}
}

.ready .hero {
	&__title {
		animation: fadeIn 0.8s 1s linear backwards;
	}
	.btn-primary {
		animation: fadeIn 0.8s 1.2s linear backwards;
	}
}
