.product-carousel {
	position: relative;
	margin-bottom: $section-margin_pc;
	overflow: hidden;
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	.product-card {
		&__price-uah {
			font-size: 14px;
		}
		&__price-usd {
			font-size: 16px;
		}
	}
	.owl-nav {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 0;
		left: -70px;
		top: 40%;
		width: calc(100% + 140px);
		position: absolute;
	}
	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.product-card {
			max-width: 400px;
			margin-left: auto;
			margin-right: auto;
		}
		.owl-dots {
			margin-top: 20px;
		}
		.section-title {
			margin-bottom: #{$section-title-margin_mobile - 20px};
		}
	}
	@include _tablet {
		.owl-dots {
			margin-top: 20px;
		}
	}
	@include _laptop-sm {
		.product-card {
			&__price-uah {
				font-size: 13px;
			}
			&__price-usd {
				font-size: 14px;
			}
		}

		.owl-nav {
			left: 0;
			width: 100%;
		}
	}
	@include _laptop {
		.owl-nav {
			left: 0;
			width: 100%;
		}
	}
}
