.catalog {
	margin-bottom: $section-margin_pc;
	&__result {
		.product-card {
			padding: 10px;
			margin-bottom: 40px;

			&__price-uah {
				font-size: 14px;
			}
			&__price-usd {
				font-size: 16px;
			}
		}
		.pagination {
			margin-top: 20px;
		}
	}
	&__recommendation {
		.section-uptitle-4 {
			margin-bottom: 15px;
		}

		.section-title-3 {
			margin-bottom: 50px;
		}
	}

	@include _mobile-tablet {
		margin-bottom: $section-margin_mobile;
		&__recommendation {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
	}
	@include _laptop {
		&__result .product-card {
			&__bottom {
				flex-wrap: wrap;
			}
		}
	}
	@include _tablet {
		&__result .product-card {
			&__bottom {
				flex-direction: column;
				align-items: center;
				justify-content: center;
			}
		}
	}
}
