.article {
	overflow: hidden;
	margin-bottom: $section-margin_pc;
	&__section {
		position: relative;
		margin-bottom: $section-margin_pc;
		&:first-child {
			margin-bottom: $section-margin_pc;
		}
		&:last-child {
			margin-bottom: 0;
		}
		.section-title {
			margin-bottom: 80px;
		}
		.ul-circle {
			@extend %ul-circle;
			li {
				margin-bottom: 20px;
				font-weight: normal;
				font-size: 16px;
				line-height: 22px;
				color: #000000;
				&::after {
					width: 8px;
					height: 8px;
				}
			}
		}
	}
	&__img {
		position: relative;
		border-radius: 15px;
		overflow: hidden;
		margin-bottom: 30px;
		width: fit-content;
		&:after {
			content: '';
			position: absolute;
			z-index: 1;
			background-color: rgba(44, 44, 44, 0.4);
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}
	}
	p {
		font-weight: normal;
		font-size: 16px;
		line-height: 22px;
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.section-title {
			margin-bottom: $section-title-margin_mobile;
			font-size: 24px;
		}
		&__section {
			margin-bottom: $section-margin_mobile;
			&:first-child {
				margin-bottom: $section-margin_mobile;
			}
		}
	}
	@include _tablet {
		margin-bottom: 70px;
		&__section {
			margin-bottom: 50px;
			&:first-child {
				margin-bottom: 50px;
			}
		}
	}
	@include _laptop-sm {
		&__section {
			margin-bottom: 70px;
			&:first-child {
				margin-bottom: 70px;
			}
		}
	}
}
