.team {
	position: relative;
	margin-bottom: $section-margin_pc;
	overflow: hidden;

	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	&__item-img {
		margin-bottom: 20px;
		// height: 380px;
		img {
			// height: 100%;
		}
	}
	&__item-name {
		font-weight: 900;
		font-size: 22px;
		line-height: 26px;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		color: #000000;
		text-align: center;
		margin-bottom: 10px;
	}
	&__item-caption {
		font-weight: normal;
		font-size: 16px;
		line-height: 22px;
		/* or 137% */
		text-align: center;
		color: $grey;
		max-width: 225px;
		margin-left: auto;
		margin-right: auto;
	}
	.owl-nav {
		position: absolute;
		top: 40%;
		transform: translateY(-50%);
		left: -70px;
		width: calc(100% + 140px);
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 0;
	}

	@include _laptop {
		.owl-nav {
			left: -30px;
			width: calc(100% + 60px);
		}
	}

	@media (min-width: 1200px) and (max-width: 1300px) {
		.owl-nav {
			left: 0;
			width: 100%;
		}
	}

	@media (min-width: 1301px) and (max-width: 1400px) {
		.owl-nav {
			left: -15px;
			width: calc(100% + 30px);
		}
	}

	@include _mobile-tablet {
		margin-bottom: $section-margin_mobile;
		.section-title,
		.section-uptitle {
			padding-left: 0;
		}
		.section-title {
			margin-bottom: #{$section-title-margin_mobile - 20px};
		}
		.owl-dots {
			margin-top: 20px;
			.owl-dot {
				margin-right: 7px;
			}
		}
		&__item-img {
			height: auto;
		}
	}
}
