.question {
	margin-bottom: $section-margin_pc;

	&__heading {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: $section-title-margin_pc;
	}
	&__item {
		margin-bottom: 40px;
		&:last-child {
			margin-bottom: 0;
		}
		&_reply {
			padding-left: 70px;
		}
	}
	&__item-heading {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		margin-bottom: 30px;
	}
	&__item-name {
		font-family: $secondary-font;
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		/* identical to box height, or 120% */
		margin-right: 15px;
		color: #000000;
		&_reply {
			color: rgba(44, 44, 44, 0.8);
		}
	}
	&__item-date {
		font-family: $secondary-font;
		font-weight: normal;
		font-size: 18px;
		line-height: 24px;
		/* identical to box height, or 133% */
		color: $grey;
	}
	&__item-text {
		font-family: $secondary-font;
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 24px;
		/* or 133% */
		color: #000000;
	}
	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.section-title {
			margin-bottom: 10px;
		}
		&__heading {
			flex-direction: column;
			margin-bottom: $section-title-margin_mobile;
		}
		&__item {
			margin-bottom: 20px;
			&_reply {
				padding-left: 20px;
			}
		}
		&__item-heading {
			flex-wrap: wrap;
			margin-bottom: 20px;
		}
		&__item-name {
			width: fit-content;
			margin-right: 10px;
			margin-bottom: 10px;
			font-size: 18px;
			line-height: 24px;
		}
		&__item-text {
			font-size: 14px;
			line-height: 20px;
		}
	}
}
