.modal {
	background: #ffffff;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	padding: 80px 30px;
	max-width: 640px;
	width: 100%;
	display: none;
	&__title {
		font-weight: 900;
		font-size: 60px;
		line-height: 70px;
		text-align: center;
		/* identical to box height */
		letter-spacing: 0.1em;
		text-transform: uppercase;
		-webkit-text-fill-color: transparent;
		-webkit-text-stroke: 2px $red;
		margin-bottom: 15px;
		&_sm {
			font-weight: 900;
			font-size: 46px;
			line-height: 54px;
		}
	}
	&__subtitle {
		font-weight: 500;
		font-size: 20px;
		line-height: 23px;
		text-align: center;
		color: $grey;
		margin-bottom: 45px;
		&_bold {
			font-weight: 800;
			font-size: 26px;
			line-height: 31px;
			text-align: center;
			text-transform: uppercase;
			color: $black-text;
			margin-bottom: 0;
		}
		&_left {
			text-align: left;
			margin-bottom: 40px;
		}
	}
	.input {
		margin-bottom: 25px;
	}
	.btn-primary {
		width: 100%;
		margin-bottom: 30px;
	}
	&__notice {
		font-family: $secondary-font;
		font-style: normal;
		font-weight: 300;
		font-size: 12px;
		line-height: 12px;
		text-align: center;
		color: $black-text;
	}
	&__cart-img {
		width: 134px;
		height: 90px;
		border-radius: 5px;
		margin-right: 20px;
	}
	&__row {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	&__cart-text {
		font-weight: bold;
		font-size: 16px;
		line-height: 22px;
		/* or 137% */
		text-transform: uppercase;
		color: $black-text;
		a {
			color: $red;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
		}
	}
	.modal__search {
		position: relative;
		.modal__subtitle {
		}
	}
	.btn-search {
		background-color: transparent;
		border: none;
		width: 24px;
		height: 24px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 5px;
		right: 20px;
		padding: 0;
		z-index: 2;
		cursor: pointer;
		svg {
			width: 100%;
			height: 100%;
			color: #000;
			fill: transparent;
		}
	}

	@include _mobile {
		padding: 30px 15px;
		&__title {
			font-size: 20px;
			line-height: 24px;
		}
		&__subtitle {
			font-size: 16px;
			line-height: 1;
			&_left {
				margin-bottom: 20px;
			}
		}
		.btn-primary {
			padding: 10px;
		}
		&__row {
			flex-direction: column;
		}
		&__cart-img {
			margin-bottom: 20px;
		}
		&__cart-text {
			text-align: center;
			font-size: 14px;
		}
	}
}

.fancybox-bg {
	background-color: rgba(0, 0, 0, 0.5);
	backdrop-filter: blur(13px);
}
