.aside {
	padding-top: 20px;
	position: fixed;
	z-index: 10;
	left: 0;
	top: 0;
	width: 80px;
	height: 100%;
	background-color: $black;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	overflow-y: auto;
	overflow-x: hidden;
	&::-webkit-scrollbar-track {
		background-color: #e5e5e5;
		border-radius: 5px;
	}
	&::-webkit-scrollbar-thumb {
		border-radius: 3px;
		background-color: $red;
	}
	&::-webkit-scrollbar {
		border-radius: 5px;
		width: 5px;
	}
	&__lang {
		padding: 0 18.5px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-bottom: 15px;
	}
	&__lang-item {
		@extend %aside-text;
		text-decoration: none;
		transition: 0.3s all;
		&_active {
			font-weight: bold;
			color: $red;
		}
		&:hover {
			color: $red;
		}
	}
	&__actions {
		width: 100%;
		margin-bottom: 80px;
	}
	&__actions-item {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 15px 0;
		border-bottom: 1px solid $border-color;
		&:hover svg {
			color: $red;
		}
		&:last-child {
			border-bottom: none;
			padding-bottom: 0;
		}
		svg {
			width: 20px;
			height: 20px;
			color: #fff;
			transition: 0.3s all;
			fill: transparent;
		}
	}
	&__social {
		width: 100%;
		margin-bottom: 64px;
	}
	&__social-item {
		width: 100%;
		padding: 15px 0;
		display: flex;
		align-items: center;
		justify-content: center;
		border-bottom: 1px solid $border-color;
		&:hover svg {
			color: $red;
		}
		&:first-child {
			padding-top: 0;
		}
		svg {
			width: 16px;
			height: 16px;
			color: #fff;
			transition: 0.3s all;
		}
	}
	&__contacts {
		width: 100%;
		margin-bottom: 64px;
	}
	&__contacts-item {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 15px 0;
		border-bottom: 1px solid $border-color;
		&:hover svg {
			color: $red;
		}
		&:first-child {
			padding-top: 0;
		}
		&:last-child {
			border-bottom: none;
			padding-bottom: 0;
		}
		svg {
			width: 16px;
			height: 16px;
			color: #fff;
			transition: 0.3s all;
		}
	}
	&__btn-callback {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: $red;
		border: none;
		border-radius: 50%;
		width: 50px;
		height: 50px;
		min-height: 50px;
		min-width: 50px;
		cursor: pointer;
		margin-left: auto;
		margin-right: auto;
		position: relative;
		z-index: 1;

		&::after {
			content: '';
			width: 60px;
			height: 60px;
			border-radius: 50%;
			background-color: rgba(184, 33, 51, 0.3);
			position: absolute;
			z-index: -1;
			animation: pulse 1.6s linear infinite;
		}
		svg {
			width: 25px;
			height: 25px;
			color: #fff;
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: scale(1.2);
			}
		}
	}

	@include _mobile-tablet {
		opacity: 0;
		visibility: hidden;
		&__lang {
			width: fit-content;
			flex-direction: column;
			margin-bottom: 0;
		}
		&__lang-item {
			margin-right: 0;
			margin-bottom: 10px;
			&:last-child {
				margin-right: 0;
				margin-bottom: 0;
			}
			&_active {
				color: #fff;
				font-weight: bold;
			}
		}
		&__actions-item {
			border-bottom: none;
			width: fit-content;
			padding: 0;
			width: 22px;
			height: 22px;
			margin-right: 20px;
			&:hover {
				svg {
					color: #fff;
					transform: scale(1.2);
				}
			}
		}
		&__social {
			display: flex;
			width: 50%;
			margin-bottom: 0;
			padding-left: 8px;
		}
		&__contacts {
			width: 50%;
			display: flex;
			margin-bottom: 0;
			justify-content: flex-end;
		}
		&__contacts-item {
			padding: 0;
			width: fit-content;
			border-bottom: none;
			padding: 0 15px;
			border-right: 1px solid $border-color;
			&:last-child {
				border-right: none;
			}
			svg {
				width: 18px;
				height: 18px;
			}
			&:hover {
				svg {
					color: #fff;
					transform: scale(1.2);
				}
			}
		}
		&__social-item {
			padding: 0;
			width: fit-content;
			border-bottom: none;
			padding: 0 15px;
			border-right: 1px solid $border-color;
			&:first-child {
				padding-left: 0;
			}
			&:last-child {
				border-right: none;
			}
			svg {
				width: 18px;
				height: 18px;
			}
			&:hover {
				svg {
					color: #fff;
					transform: scale(1.2);
				}
			}
		}
		&__btn-callback {
			margin-right: 0;
			margin-left: 0;
			width: 22px;
			height: 22px;
			min-width: 22px;
			min-height: 22px;
			padding: 0;
			svg {
				width: 100%;
				height: 100%;
			}
			&::after {
				content: none;
			}
		}
	}
	@include _mobile {
		&__actions-item {
			margin-right: 0;
		}
	}
	@include _laptop-sm {
		width: 60px;
	}
	@include _laptop {
		width: 60px;
		&__btn-callback {
			width: 40px;
			height: 40px;
			min-height: 40px;
			min-width: 40px;
			svg {
				width: 20px;
				height: 20px;
			}
		}
	}
}
.js-cart {
	margin-right: 0;
}
