.recomendation {
	position: relative;
	margin-bottom: #{$section-margin_pc - 40px};
	overflow: hidden;
	&__heading {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 75px;
	}
	.product-card {
		margin-bottom: 40px;
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		&__heading {
			flex-wrap: wrap;
			margin-bottom: 50px;
			.btn-primary {
				margin-left: auto;
				margin-right: auto;
			}
		}
	}

	.row-carousel {
		margin: 0;
	}
}
