.sort {
	@extend %sort-text;
	margin-bottom: 50px;
	margin-left: auto;
	width: fit-content;
	position: relative;
	&__toggle {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		transition: 0.3s all;
	}
	&__caption {
		color: $black-text;
		margin-right: 5px;
	}
	&__option {
		cursor: pointer;
		color: $red;
		text-decoration: underline;
	}
	&__menu {
		display: none;
		position: absolute;
		z-index: 2;
		background-color: rgba(255, 255, 255, 0.7);
		backdrop-filter: blur(4px);
		padding: 5px;
		right: 26px;
		top: 100%;
		border-radius: 15px;
	}
	&__menu-item {
		text-align: right;
		padding: 5px 0;
		cursor: pointer;
		font-size: 14px;
		transition: 0.3s all;
		color: $black-text;
		text-align: right;
		text-decoration: none;
		display: block;
		&:hover {
			color: $red;
		}
	}
	&__arrow {
		width: 16px;
		height: 16px;
		margin-left: 10px;
		cursor: pointer;
		transition: 0.3s all;
		svg {
			width: 100%;
			height: 100%;
		}
		&_active {
			transform: rotate(180deg);
		}
	}
	@include _mobile-tablet {
		&__menu {
			right: 0;
		}
	}
	@include _mobile {
		&__toggle {
			flex-wrap: wrap;
			justify-content: flex-end;
		}
		&__arrow {
			width: 10px;
			height: 10px;
		}
	}
}
