.reviews-card {
	position: relative;
	background-color: $black-2;
	border-radius: 15px;
	padding: 60px 20px 60px 60px;
	min-height: 322px;
	&__date {
		font-weight: bold;
		font-size: 14px;
		line-height: 18px;
		color: $red;
		transform: rotate(-90deg);
		transform-origin: top left;
		position: absolute;
		left: 20px;
		top: 30%;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 2;
		&::before {
			content: '';
			position: absolute;
			width: 205px;
			height: 1px;
			right: 100%;
			margin-right: 20px;
			background-color: rgba(178, 178, 178, 0.22);
		}
	}
	&__name {
		@extend %card-title-3;
		color: #fff;
		margin-bottom: 40px;
		position: relative;
		z-index: 2;
	}
	&__heading {
		position: relative;
		z-index: 2;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 40px;
		.reviews-card__name {
			margin-bottom: 0 !important;
		}
	}
	&__text {
		@extend %card-text-secondary;
		color: #fff;
		z-index: 2;
	}
	&__gallery {
		margin-top: 20px;
		img {
		}
	}

	@include _mobile {
		max-height: 500px;
		overflow: hidden;
		&__date {
			top: 27%;
			&::before {
				width: 380px;
			}
		}
		&__text {
			max-height: 320px;
			overflow: hidden;
		}
	}
}
//
.videoreviews-card {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	border-radius: 15px;
	overflow: hidden;
	padding: 60px;
	min-height: 322px;
	position: relative;
	.reviews-card__name {
		margin-bottom: 52px;
	}
	&::after {
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		border-radius: 15px;
		background-color: rgba(28, 28, 28, 0.8);
	}
	.btn-play {
		position: relative;
		z-index: 2;
	}

	@include _mobile {
		padding: 30px 40px;
		.reviews-card__name {
			margin-bottom: 85px;
		}
		.btn-play {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
}
