.about {
	.section-uptitle-3 {
		margin-bottom: 10px;
	}
	.section-title-3 {
		margin-bottom: 30px;
	}
	p {
		margin-top: 0;
		font-weight: normal;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__section {
		margin-bottom: 80px;
		&:first-child {
			margin-bottom: 40px;
		}
	}
	&__img {
		position: relative;
		overflow: hidden;
		&::after {
			top: 0;
			left: 0;
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: rgba(44, 44, 44, 0.5);
		}
		&_no-filter {
			&:after {
				content: none;
			}
		}

		&_left {
			border-radius: 15px 0 0 15px;
		}

		&_right {
			border-radius: 0 15px 15px 0;
		}
	}
	&__video {
		position: relative;
		&::after {
			content: '';
			background: rgba(44, 44, 44, 0.8);
			border-radius: 15px;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			position: absolute;
		}
		img {
			border-radius: 15px;
		}
		.btn-play {
			position: absolute;
			z-index: 2;
			top: 55%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}

	@include _mobile {
		&__img {
			border-radius: 15px;
			margin-top: 20px;
		}
		&__section {
			margin-bottom: 40px;
		}
	}
	@include _tablet {
		&__img {
			max-width: 600px;
			border-radius: 15px;
			// margin-left: auto;
			margin-right: auto;
			margin-top: 20px;
			img {
				object-fit: cover;
				height: 100%;
			}
		}
		&__section {
			margin-bottom: 40px;
		}
	}
}
