.reviews-article {
	margin-bottom: $section-margin_pc;
	&__video {
		position: relative;
		margin-bottom: 80px;
		max-height: 566px;
		img {
			border-radius: 11px;
			height: 100%;
		}
		.btn-play {
			position: absolute;
			z-index: 2;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
	p {
		color: $black-text;
		margin-bottom: 20px;
		margin-top: 0;
		&:last-child {
			margin-bottom: 0;
		}
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		&__video {
			margin-bottom: 30px;
			img {
				height: auto;
			}
		}
	}
	@include _tablet {
		margin-bottom: 70px;
		&__video {
			height: auto;
			margin-bottom: 40px;
			img {
				height: auto;
			}
		}
	}

	@include _laptop-sm {
		&__video {
			img {
				height: auto;
			}
		}
	}
	@include _laptop {
		&__video {
			height: 400px;
			width: fit-content;
			margin-left: auto;
			margin-right: auto;
		}
	}

	@include _large {
		&__video {
			height: 400px;
			width: fit-content;
			margin-left: auto;
			margin-right: auto;
		}
	}
}
