.consultation {
	min-height: 890px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	color: #fff;
	margin-bottom: $section-margin_pc;
	background-color: $black-text;
	&__title {
		margin-bottom: 35px;
		.section-uptitle {
			margin-bottom: 8px;
			color: #fff;
			&:last-child {
				margin-bottom: 0;
			}
		}
		.section-title {
			margin-bottom: 8px;
		}
	}
	&__wrapper {
		padding-left: 50px;
		padding-top: 54px;
		padding-bottom: 54px;
	}
	.subtitle-1 {
		margin-bottom: 50px;
	}
	.title-2 {
		margin-bottom: 40px;
	}
	&__form {
		max-width: 430px;
		width: 100%;
		.title-6 {
			margin-bottom: 23px;
		}

		.input {
			margin-bottom: 15px;
		}
	}
	&__caption {
		@extend %caption-text;
		margin-top: 15px;
	}
	ul {
		@extend %ul-circle;
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
		margin-bottom: 40px;
		li {
			margin-right: 37px;
			max-width: 225px;
			margin-bottom: 20px;
		}
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.subtitle-1,
		.title-2 {
			text-align: center;
		}
		&__wrapper {
			padding: 70px 0 0 0;
		}
		&__form {
			margin-bottom: 15px;
			.title-6 {
				font-size: 14px;
				line-height: 16px;
				text-align: center;
			}
		}
		&__caption {
			text-align: center;
		}
		ul {
			margin-bottom: 38px;
			li {
				margin-right: 19px;
				max-width: none;
				width: calc(50% - 19px);
			}
		}
	}
	@include _tablet {
		&__wrapper {
			padding-top: 79px;
			padding-left: 0;
		}
		.subtitle-1,
		.title-2 {
			text-align: center;
		}
		&__form {
			margin-left: auto;
			margin-right: auto;
			.title-6 {
				text-align: center;
			}
		}
		&__caption {
			text-align: center;
		}
		ul {
			justify-content: center;
		}
	}
	@include _laptop-sm {
		&__form {
			margin-right: auto;
			margin-left: auto;
		}
		ul {
			justify-content: center;
		}
	}
}

.consultation-team {
	position: relative;
	min-height: 890px;
	img {
		position: absolute;
		bottom: 0;
		width: 812px;
		height: 768px;
		max-width: none;
		right: 0;
	}
	&__name {
		margin-bottom: 10px;
		@extend %card-title-2;
	}
	&__caption {
		@extend %card-text-2;
		color: $grey;
	}
	&__card {
		position: absolute;
		background: linear-gradient(269.83deg, #2c2c2c 0.08%, #1c1c1c 99.8%), #1c1c1c;
		max-width: 250px;
		&_1 {
			bottom: 205px;
			left: -120px;
			padding: 19px 42px 19px 20px;
			border-left: 2px solid $red;
		}
		&_2 {
			bottom: 123px;
			right: 0;
			padding: 19px 20px 19px 42px;
			text-align: right;
			border-right: 2px solid $red;
		}
	}
	.error {
		opacity: 0.1;
	}

	@include _mobile-tablet {
		min-height: auto;
		img {
			position: relative;
			height: auto;
			width: 100%;
		}
		&__card {
			&_1 {
				left: 0;
			}
		}
	}
	@include _mobile {
		min-height: 310px;
		img {
			position: absolute;
			height: 310px;
			width: auto;
		}

		&__card {
			max-width: 142px;
			&_1 {
				padding: 5px 15px 5px 7px;
				bottom: 70px;
			}
			&_2 {
				padding: 5px 7px 5px 15px;
				bottom: 30px;
			}
		}
	}
	@include _laptop-sm {
		min-height: 723px;
		img {
			width: 750px;
			height: auto;
		}
		&__card {
			&_1 {
				left: 140px;
			}
			&_2 {
				right: 30px;
			}
		}
	}
	@include _laptop {
		img {
			width: 650px;
			height: auto;
		}
		&__card {
			&_1 {
				left: 0;
			}
		}
	}

	@media (min-width: 1200px) and (max-width: 1250px) {
		img {
			width: 600px;
			height: auto;
		}
	}
}
