.gallery {
	margin-bottom: 60px;
	.section-title {
		margin-bottom: $section-title-margin_pc;
		.black {
			-webkit-text-stroke: 3px $black-text;
		}
	}
	&__row {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
		margin-bottom: 60px;
	}
	&__img {
		width: calc(25% - 10px);
		margin-right: 10px;
		margin-bottom: 10px;
		height: 320px;
		overflow: hidden;
		display: flex;
		align-items: center;
		justify-content: center;
		img {
			height: 100%;
			transition: 0.3s all;
			object-fit: cover;
		}
		&_big {
			width: calc(50% - 10px);
		}
		&_last {
			margin-right: 0;
			width: 25%;
		}
		&_big-last {
			width: 50%;
		}
		&_full {
			width: 100%;
			height: 100%;
		}
	}
	@include _mobile {
		.section-title {
			margin-bottom: $section-title-margin_pc;
		}
		&__row {
			align-items: stretch;
		}
		&__img {
			width: calc(50% - 6px);
			margin-right: 6px;
			height: auto;
			&:nth-child(odd) {
				// margin-left: 3px;
				margin-right: 0;
			}
			&:first-child {
				margin-right: 6px;
			}
			&_big {
				width: 100%;
				margin-right: 0;
			}
			&_full {
				width: 100%;
			}
		}
	}
	@include _tablet {
		.section-title {
			margin-bottom: 30px;
		}
		&__img {
			height: auto;
		}
		&__row {
			align-items: stretch;
		}
	}
	@include _laptop-sm {
		&__row {
			align-items: stretch;
		}
		&__img {
			height: auto;
		}
	}

	@media (min-width: 1024px) {
		&__img:hover {
			img {
				transform: scale(1.1);
			}
		}
	}
}
