.store {
	position: relative;
	margin-bottom: $section-margin_pc;
	overflow: hidden;
	&__item {
		padding: 40px 0;
		transition: 0.6s all;
		margin-top: 40px;
		position: relative;
		&:after {
			transition: 0.3s all;
			content: '';
			width: 100%;
			height: calc(100% - 80px);
			position: absolute;
			top: 40px;
			left: 0;
			background-color: rgba(44, 44, 44, 0.8);
		}
	}
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	.owl-carousel {
		.owl-stage-outer {
			min-height: 522px;
			padding-top: 20px;
			padding-bottom: 20px;
		}
		.center {
			z-index: 2;
			.store__item {
				margin-top: 0;
				padding: 80px 0;
				transform: scale(1.6);
				overflow: hidden;
				&:after {
					opacity: 0;
					visibility: hidden;
				}
				img {
					border-radius: 15px;
					max-height: 495px;
				}
			}
		}
	}
	.owl-nav {
		justify-content: space-between;
		position: absolute;
		display: flex;
		align-items: center;
		height: 0;
		left: 420px;
		top: 50%;
		width: calc(100% - 840px);
	}
	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}

		.store__item {
			padding: 0;
			margin-top: 0;
			height: 280px;
			img {
				height: 100% !important;
				width: auto;
				object-fit: cover;
			}
			&:after {
				height: 100%;
				top: 0;
			}
		}
		.owl-dots {
			margin-top: 20px;
		}
		.owl-carousel {
			.owl-stage-outer {
				min-height: 295px;
				padding: 0;
			}
			.center {
				.store__item {
					transform: none !important;
					padding: 0;
					img {
						max-height: none;
						border-radius: 0;
					}
				}
			}
		}
	}
	@include _tablet {
		.owl-carousel {
			.owl-stage-outer {
				min-height: 330px;
			}
		}
		.section-title {
			margin-bottom: 30px;
		}
	}

	@include _laptop-sm {
		margin-bottom: 70px;
		.owl-carousel {
			.owl-stage-outer {
				min-height: 360px;
			}
		}
		.owl-nav {
			left: 40px;
			width: calc(100% - 80px);
		}
	}

	@include _laptop {
		.owl-nav {
			top: 40%;
			left: 240px;
			width: calc(100% - 480px);
		}
	}
}
