.tabs {
	margin-bottom: $section-margin_pc;
	// overflow: hidden;
	.container {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
	}

	ul {
		@extend %ul-reset;
		top: 140px;
		max-width: 225px;
		width: 100%;
		min-width: 225px;
		margin-right: 30px;
		li {
			margin-bottom: 15px;
			&:last-child {
				margin-bottom: 0;
			}
			a {
				font-family: $secondary-font;
				font-weight: 900;
				font-size: 16px;
				line-height: 18px;
				text-align: center;
				letter-spacing: 0.1em;
				text-transform: uppercase;
				color: #ffffff;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				padding: 23px 10px;
				background-color: $black-text;
				border-radius: 15px;
				text-decoration: none;
				transition: 0.3s all;
				&:hover {
					background-color: $red;
				}
			}
		}
		.active {
			a {
				background-color: $red;
			}
		}
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.container {
			flex-wrap: wrap;
		}
		ul {
			max-width: none;
			min-width: auto;
			margin-right: 0;
			margin-bottom: 40px;
			li {
				margin-bottom: 15px;
				a {
					font-size: 14px;
					line-height: 18px;
					text-align: center;
					padding: 14px 20px;
				}
			}
		}
	}

	@include _tablet {
		ul {
			top: 100px;
			li a {
				font-size: 14px;
				padding: 15px 10px;
			}
		}
	}

	@include _laptop-sm {
		ul li a {
			font-size: 14px;
			padding: 17px 10px;
		}
	}
	@media (min-width: 1024px) {
		position: sticky;
	}
}

.tabs-content {
	display: none;
	overflow: hidden;
	&__table {
		width: 100%;
		background-color: $black-text;
		border-radius: 15px;
		position: relative;
		z-index: 1;
		&::after {
			content: '';
			position: absolute;
			background-color: #222222;
			border-radius: 15px 0px 0px 15px;
			width: 50%;
			height: 100%;
			left: 0;
			top: 0;
			z-index: -1;
		}
	}
	&__size {
		display: flex;
		align-items: stretch;
		justify-content: flex-start;
		flex-wrap: wrap;
		width: 100%;
	}
	&__item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		color: #fff;
		padding: 15px 30px;
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		/* identical to box height */
		color: #ffffff;
		border-bottom: 1px solid rgba(179, 179, 179, 0.22);
	}
	&__key {
		width: 50%;
	}
	&__value {
		width: 50%;
		text-align: right;
	}
	@include _mobile {
		&__table::after {
			content: none;
		}
		&__item {
			flex-wrap: wrap;
			padding: 0;
		}
		&__key {
			background-color: #222222;
		}
		&__key,
		&__value {
			width: 100%;
			padding: 10px 5px;
			text-align: center;
		}
	}
	@include _tablet {
		&__key,
		&__value {
			width: 40%;
		}
	}
}

.size-card {
	width: 25%;
	display: flex;
	align-items: center;
	justify-content: stretch;
	flex-direction: column;
	padding: 25px 0;
	border-bottom: 1px solid rgba(179, 179, 179, 0.22);
	border-right: 1px solid rgba(179, 179, 179, 0.22);
	position: relative;
	&_no-rb {
		border-right: none;
	}
	&:after {
		content: '';
		width: 110px;
		height: 50px;
		position: absolute;
		bottom: -25px;
		right: -55px;
		background-color: #fff;
		z-index: 2;
	}
	&__caption {
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		color: $black-text;
		margin-bottom: 10px;
		max-width: 130px;
		text-align: center;
		min-height: 38px;
	}
	&__img {
		margin-bottom: 20px;
		width: 130px;
		height: 130px;
		img {
			height: 100%;
		}
	}
	&__number {
		font-weight: bold;
		font-size: 18px;
		line-height: 21px;
		color: $red;
		max-width: 130px;
		text-align: center;
		span {
			font-weight: normal;
			display: block;
		}
	}
	// &:nth-last-child(-n + 4) {
	// 	&::after {
	// 		content: none;
	// 	}
	// 	border-bottom: none;
	// }
	@include _mobile {
		border: none;
		width: 50%;
		&:after {
			content: none;
		}
	}
	@include _tablet {
		width: 50%;
		&:nth-last-child(-n + 2) {
			&::after {
				content: none;
			}
			border-bottom: none;
		}

		&:nth-last-child(odd) {
			border-right: none;
		}

		&__number span {
			font-size: 14px;
		}
	}

	@include _laptop-sm {
		&__number span {
			font-size: 14px;
		}
	}
	@media (min-width: 1024px) {
		&:nth-last-child(-n + 4) {
			&::after {
				content: none;
			}
			border-bottom: none;
		}
	}
}
