.seo {
	position: relative;
	min-height: 300px;
	&::before {
		content: '';
		background: linear-gradient(180deg, rgba(255, 255, 255, 0) 84%, rgba(255, 255, 255, 1) 100%);
		width: 100%;
		height: 100%;
		position: absolute;
		transition: 0.3s all;
	}
	&_active {
		// margin-bottom: 100px;
		&::before {
			opacity: 0;
			visibility: hidden;
		}
	}

	&__container {
		margin-bottom: $section-margin_pc;
	}

	@include _mobile {
		text-align: center;

		&__container {
			margin-bottom: $section-margin_mobile;
		}
		&:before {
			left: 0;
		}
	}
}
.readmore-js-toggle {
	margin-top: 10px;
	display: block;
	width: fit-content !important;
	font-weight: 800;
	font-size: 16px;
	line-height: 22px;
	/* identical to box height, or 137% */
	text-decoration: underline;
	color: $red;

	@include _mobile {
		margin-left: auto;
		margin-right: auto;
	}
}
