.e404 {
	padding-bottom: 60px;
	.breadcrumbs {
		margin-top: 43px;
	}
	&__img {
		margin-bottom: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		margin-left: auto;
		margin-right: auto;
	}
	&__title {
		font-family: Raleway;
		font-style: normal;
		font-weight: 900;
		font-size: 50px;
		line-height: 59px;
		/* identical to box height */
		text-align: center;
		color: #ffffff;
		margin-bottom: 20px;
	}
	&__subtitle {
		font-weight: 600;
		font-size: 22px;
		line-height: 30px;
		/* or 136% */
		text-align: center;
		color: $grey;
		max-width: 980px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 30px;
	}
	.btn-primary {
		margin-right: auto;
		margin-left: auto;
	}
	@include _mobile {
		&__title {
			font-size: 38px;
			line-height: 1;
		}
		&__subtitle {
			font-size: 18px;
			line-height: 26px;
		}
		.btn-primary {
			min-width: auto;
			max-width: none;
		}
	}
}
