.cart-product-card {
	padding: 20px;
	border-bottom: 1px solid rgba(44, 44, 44, 0.3);
	position: relative;
	&__img {
		width: 174px;
		height: 110px;
	}
	&__title {
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		text-transform: uppercase;
		color: $black-text;
		text-decoration: none;
		max-width: 310px;
	}
	&__price {
		font-weight: 600;
		font-size: 16px;
		line-height: 19px;
		max-width: 200px;
		width: 100%;
		color: $red;
	}
	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		max-width: 520px;
		width: 100%;
	}
	&__caption {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	.btn-delete {
		cursor: pointer;
		background-color: transparent;
		border: none;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0;
		position: absolute;
		right: 35px;
		top: 50%;
		transform: translateY(-50%);
		svg {
			width: 24px;
			height: 24px;
			color: $black-text;
			transition: 0.6s all;
		}
		&:hover {
			svg {
				transform: rotate(180deg);
				color: $red;
			}
		}
	}
	@include _mobile {
		&__img {
			width: fit-content;
			margin-left: auto;
			margin-right: auto;
			height: auto;
			margin-bottom: 20px;
		}
		&__price {
			margin-left: auto;
			margin-right: auto;
			b {
				display: block;
			}
		}
		.btn-delete {
			top: 15px;
			right: 5px;
		}
		&__title {
			font-size: 14px;
			line-height: 18px;
		}
		.quantity {
			margin-left: auto;
			margin-right: auto;
			min-height: auto;
			input {
				height: 28px;
			}
			button {
				height: 28px;
			}
		}
		&__caption {
			display: block;
			opacity: 1;
			visibility: visible;
			margin-bottom: 30px;
			padding: 10px;
			font-size: 12px;
			font-weight: bold;
			line-height: 1;
			// text-transform: uppercase;
			// letter-spacing: 0.1em;
			color: #fff;
			background-color: #000;
			border-radius: 20px;
		}
	}
	@include _tablet {
		padding: 20px 10px;
		&__img {
			width: 100%;
			height: auto;
		}
		&__price {
			text-align: center;
			b {
				display: block;
			}
		}
	}
	@media (min-width: 1024px) and (max-width: 1336px) {
		&__img {
			width: 100%;
			height: auto;
		}
	}
}
