.how-banner {
	margin-bottom: $section-margin_pc;
	&__wrapper {
		background-repeat: no-repeat;
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		background-image: url(../img/how-banner-bg.jpg);
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__img {
		width: auto;
		height: 234px;
		img {
			height: 100%;
			width: auto;
		}
	}
	&__heading {
		text-align: right;
		.section-title {
			padding-bottom: 10px;
			position: relative;
			text-decoration: none;
			&::after {
				content: '';
				display: block;
				width: 100%;
				height: 2px;
				background-color: $red;
				position: absolute;
				bottom: 0;
				right: 0;
				transition: 0.3s all;
			}
			&:hover {
				&::after {
					opacity: 0;
					visibility: hidden;
					width: 0;
				}
			}
		}
	}
	@include _mobile {
		margin-bottom: $section-margin_mobile;
		&__wrapper {
			justify-content: flex-end;
			padding: 40px 0;
			padding-left: 70px;
		}
		&__img {
			position: absolute;
			left: -200px;
			height: 190px;
		}
		&__heading {
			.section-uptitle {
				text-align: right;
			}
		}
	}
}
