.filter {
	background-color: #fff;
	margin-bottom: 130px;
	&__price {
		transition: 0.3s all;
		padding-bottom: 30px;
		border-bottom: 1px solid rgba(179, 179, 179, 0.22);
		margin-bottom: 40px;
	}
	&__heading {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 15px;
	}
	&__title {
		@extend %card-title-2;
	}
	&__toggle {
		cursor: pointer;
		background-color: transparent;
		width: 32px;
		height: 32px;
		border-radius: 50%;
		border: 1px solid $red;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0;
		transition: 0.3s all;
		svg {
			width: 16px;
			height: 16px;
			color: $black-text;
			transform: rotate(-90deg);
		}
		&_close {
			transform: rotate(180deg);
		}
	}
	&__options {
		transition: 0.3s all;
		padding-bottom: 30px;
		margin-bottom: 30px;
		border-bottom: 1px solid rgba(179, 179, 179, 0.22);
		.checkbox {
			margin-bottom: 10px;
			align-items: center;
			.checkmark {
				margin-top: 2px;
			}
			&__icon {
				min-width: 30px;
				width: 30px;
				height: 30px;
				margin-right: 15px;
				svg {
					width: 100%;
					height: 100%;
					color: $black-text;
				}
			}
			input:checked ~ .checkbox__icon svg {
				color: $red;
			}
		}
		&:last-child {
			border-bottom: none;
		}
	}
	&__color {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
		// padding-bottom: 30px;
		// margin-bottom: 30px;
		// border-bottom: 1px solid rgba(179, 179, 179, 0.22);
		.checkbox {
			margin-bottom: 15px;
		}
	}
	&__top {
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	.close {
		padding: 0;
	}
	.price-ranges {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
		position: relative;
		margin-bottom: 15px;
		&__input {
			display: inline-flex;
			align-items: flex-start;
			justify-content: flex-start;
			align-content: flex-start;
			flex-direction: column;
			margin-right: 30px;

			input {
				// border: 1px solid $grey-text-color;
				outline: none;
				width: 100%;
				font-weight: normal;
				font-size: 12px;
				line-height: 14px;
				/* identical to box height, or 117% */
				font-feature-settings: 'pnum' on, 'lnum' on, 'liga' off, 'kern' off;
				border: 1px solid rgba(179, 179, 179, 0.5);
				border-radius: 5px;
				// color: $grey-text-color;
				padding: 5px 15px;
				max-width: 85px;
				width: 100%;
			}
		}
		&__input-caption {
			margin-bottom: 4px;
			font-weight: 500;
			font-size: 12px;
			line-height: 14px;
			/* identical to box height, or 117% */
			font-feature-settings: 'pnum' on, 'lnum' on;
			color: #000000;
		}
	}
	.noUi {
		&-handle {
			top: -5px !important;
			display: flex;
			align-items: center;
			justify-content: center;
			align-content: center;
			border: 1px solid $red !important;
			right: -5px !important;
			border-radius: 50%;
			box-shadow: 0px 0px 1px rgba(44, 44, 44, 0.75), 0px 0px 4px rgba(183, 32, 50, 0.31);
			outline: none;
			cursor: pointer;
			width: 18px !important;
			height: 18px !important;

			&::after,
			&::before {
				content: none;
			}
			&::before {
				content: '';
				width: 10px;
				height: 10px;
				background-color: $red;
				border-radius: 50%;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
			}
		}
		&-target {
			box-shadow: none;
			border: none;
		}
		&-horizontal {
			height: 8px;
			border-radius: 4px;
			background-color: #222222;
		}
		&-connect {
			// background-color: $orange;
			background-color: $red;
			height: 8px;
		}
	}
	&__mobile-btn {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	@include _mobile-tablet {
		pointer-events: none;
		transform: translateY(-200%);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999;
		width: 100%;
		height: 100%;
		overflow-y: auto;
		transition: 0.6s all;
		&_active {
			pointer-events: inherit;
			transform: translateY(0%);
		}
		&__mobile-btn {
			display: block;
			padding: 15px 10px;
			border-radius: 15px;
			min-width: 140px;
			opacity: 1;
			visibility: visible;
		}
		&__top {
			opacity: 1;
			visibility: visible;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			font-weight: 600;
			font-size: 16px;
			line-height: 14px;
			text-transform: uppercase;
			color: $black-text;
			padding: 15px 8px;
			background-color: rgba(179, 179, 179, 0.22);
			margin-bottom: 30px;
			&::before {
				content: '';
				width: 16px;
				height: 16px;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 100% 100%;
				background-image: url(../img/arrow-black.svg);
				margin-right: 15px;
			}
		}
		&__options,
		&__price {
			padding-left: 8px;
			padding-right: 8px;
		}
		.close {
			padding: 0 8px;
		}
	}
}
