.owl-nav {
	button {
		width: 44px;
		height: 44px;
		border-radius: 50%;
		background-color: $red !important;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: 0.3s all;
		svg {
			color: #fff;
			transition: inherit;
		}

		&:hover {
			background-color: rgba(179, 179, 179, 0.5) !important;
			svg {
				color: $black-text;
			}
		}
	}
}

.owl-dots {
	display: flex;
	align-items: center;
	justify-content: center;
	.owl-dot {
		width: 12px;
		height: 12px;
		border-radius: 50%;
		background-color: rgba(179, 179, 179, 0.22) !important;
		transition: 0.3s all;
		margin-right: 15px;
	}
	.active {
		background-color: $red !important;
		box-shadow: 0px 3px 12px rgba(184, 33, 51, 0.4);
	}

	@include _mobile {
		.owl-dot {
			width: 10px;
			height: 10px;
		}
	}
}
