.video-reviews {
	margin-bottom: $section-margin_pc;
	position: relative;
	&__item {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-direction: column;
		margin-bottom: 40px;
		&:hover .btn-play svg {
			transform: scale(1.2);
		}
	}
	&__item-preview {
		position: relative;
		margin-bottom: 20px;
		border-radius: 10px;
		overflow: hidden;
		&:after {
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			position: absolute;
			background: rgba(44, 44, 44, 0.8);
		}
		.btn-play {
			position: absolute;
			top: 50%;
			z-index: 2;
			left: 50%;
			transform: translate(-50%, -50%);
			svg {
				margin-bottom: 0;
				transition: 0.3s all;
			}

			&:hover svg {
				transform: scale(1.2);
			}
		}
	}
	&__item-title {
		@extend %video-reviews-title;
		text-decoration: none;
		margin-bottom: 20px;
	}
	&__item-details {
	}
	.pagination {
		margin-top: 20px;
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		&__item {
			max-width: 340px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 20px;
		}
		&__item-title {
			max-height: 16px;
			min-height: 16px;
			overflow: hidden;
			margin-bottom: 10px;
		}
		&__item-preview {
			margin-bottom: 10px;
		}
	}

	@include _tablet {
		margin-bottom: 70px;
		&__item-title {
			max-height: 21px;
			min-height: 21px;
			overflow: hidden;
		}
	}
}
