.numbers {
	position: relative;
	padding-bottom: $section-margin_pc;
	overflow: hidden;
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	&__row {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}
	&__item {
		display: flex;
		align-items: center;
		text-align: center;
		justify-content: flex-start;
		flex-direction: column;
	}
	&__item .btn-primary {
		font-size: 12px;
		line-height: 14px;
		margin-top: 32px;
		padding: 12px 10px 8px 10px;
	}
	&__item-title {
		font-style: normal;
		font-weight: 900;
		font-size: 80px;
		line-height: 94px;
		/* identical to box height */
		color: $black-text;
		position: relative;
		margin-bottom: 35px;
		span {
			font-size: 40px;
		}
	}
	&__item-icon {
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 50px;
			height: 50px;
			right: 0;
			bottom: -20px;
		}
		&_2 {
			width: 70px;
			height: 50px;
			top: -3px;
			right: 18px;
		}
		&_3 {
			width: 50px;
			height: 50px;
			top: -18px;
			right: 3px;
		}
		&_4 {
			width: 50px;
			height: 50px;
			right: 40px;
			top: -15px;
		}
	}
	&__item-text {
		font-weight: bold;
		font-size: 20px;
		line-height: 23px;
		text-align: center;
		color: $black-text;
		a {
			color: $red;
			&:hover {
				text-decoration: none;
			}
		}
	}

	@include _mobile {
		padding-bottom: $section-margin_mobile;
		&__item-title {
			font-weight: 900;
			font-size: 40px;
			line-height: 47px;
		}

		&__row {
			flex-wrap: wrap;
		}
		&__item {
			width: 50%;
			margin-bottom: 40px;
		}

		&__item .btn-primary {
			margin-top: 22px;
		}
		&__item-title {
			font-weight: 900;
			font-size: 40px;
			line-height: 47px;
			margin-bottom: 15px;
			span {
				font-size: 20px;
			}
		}
		&__item-text {
			font-size: 14px;
			line-height: 16px;
		}
		&__item-icon {
			width: 24px;
			height: 24px;
			&_4 {
				right: 0;
			}
		}
	}

	@include _tablet {
		.section-title {
			margin-bottom: 50px;
		}
		&__row {
			flex-wrap: wrap;
		}
		&__item {
			width: 50%;
			margin-bottom: 20px;
		}
		&__item-title {
			font-size: 60px;
			line-height: 1;
		}

		&__item-icon {
			width: 34px;
			height: 34px;
		}
	}

	@include _laptop-sm {
		&__item-title {
			font-size: 68px;
			line-height: 86px;
		}
	}

	@include _laptop {
		&__item-title {
			font-size: 68px;
			line-height: 86px;
		}
	}
}
