.menu {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	li {
		position: relative;
		&:first-child {
			.menu__link {
				padding-left: 0;
			}
		}
	}
	&__link {
		display: block;
		padding: 0 15px;
		@extend %header-text;
		text-decoration: none;
		transition: 0.3s all;
		&:hover {
			color: $red;
		}
	}
	&__sticker {
		position: absolute;
		@extend %sticker-text;
		padding: 2px 7px;
		background-color: $red;
		border-radius: 15px;
		box-shadow: 0px 0px 4px rgba(183, 32, 50, 0.3);
		bottom: 100%;
		right: 0;
	}
	@include _mobile-tablet {
		width: fit-content;
		align-items: stretch;
		margin-bottom: 20px;
		flex-direction: column;
		&__link {
			padding: 5px 0;
			text-align: center;
			&:hover {
				color: $black-text;
			}
		}

		&__sticker {
			background-color: $black;
			position: static;
			margin-left: auto;
			display: block;
			width: fit-content;
			&_red {
				background-color: $red;
			}
		}
	}

	@include _mobile {
		align-items: flex-start;
		width: 100%;
		padding-left: 8px;
	}
	@include _laptop-sm {
		&__link {
			padding: 0 5px;
		}
	}
}
