.rating-wrapper {
	display: flex;
	align-items: center;
	height: 27px;
	width: fit-content;
}
.rating {
	border: none;
	float: left;
	padding: 0;
	margin: 0;
	&__caption {
		font-family: $secondary-font;
		display: inline-block;
		font-weight: bold;
		font-size: 16px;
		line-height: 24px;
		/* identical to box height, or 150% */
		color: $black-text;
		margin-right: 15px;
	}
	input {
		cursor: pointer;
		display: none;
	}
	label {
		float: right;
		width: 18px;
		height: 18px;
		cursor: pointer;
		display: inline-block !important;
		cursor: pointer;
		margin-right: 10px;
		svg {
			width: 18px;
			height: 18px;
			stroke: $red;
			color: transparent;
		}
		&:first-of-type {
			margin-right: 0;
		}
	}
	label:before {
	}
	input:checked ~ label,
	&:not(:checked) > label:hover,
	&:not(:checked) > label:hover ~ label {
		color: $red;
		svg {
			color: $red;
		}
	}
	input:checked + label:hover,
	input:checked ~ label:hover,
	label:hover ~ input:checked ~ label,
	input:checked ~ label:hover ~ label {
		color: $red;
		svg {
			color: $red;
		}
	}
}
