.faq {
	position: relative;
	margin-bottom: $section-margin_pc;
	overflow: hidden;
	ul {
		@extend %ul-line;
		margin-bottom: 30px;
		li {
			margin-bottom: 10px;
		}
	}
	p {
		@extend %faq-text;
		margin-bottom: 30px;
		margin-top: 0;
	}
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
	}
}
.faq-item {
	margin-bottom: 5px;
	position: relative;
	&:last-child {
		margin-bottom: 0;
	}
	&__question {
		padding: 23px 40px;
		padding-right: 66px;
		border-radius: 15px;
		background-color: $red;
		color: #fff;
		@extend %title-3;
		transition: 0.3s all;
		&_active {
			background-color: rgba(185, 52, 68, 0.87);
		}
	}
	&__toggle {
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 26px;
		height: 26px;
		top: 21px;
		// background-color: #fff;
		background-color: transparent;
		z-index: 2;
		right: 40px;
		border: none;
		outline: none;
		cursor: pointer;
		padding: 0;
		overflow: hidden;
		span {
			background-color: #fff;
			width: 26px;
			height: 2px;
			display: block;
			position: absolute;
			border-radius: 2px;
			transition: 0.3s all;
			&:nth-child(2) {
				transform: rotate(90deg);
				transform-origin: center;
			}
		}
		&_active {
			span {
				&:nth-child(2) {
					transform: rotate(90deg) translateX(-100%);
				}
			}
		}
	}
	&__answer {
		padding: 40px 145px 40px 40px;
		display: none;
	}
	@include _mobile {
		&__question {
			padding: 15px;
			padding-right: 35px;
		}
		&__toggle {
			width: 18px;
			height: 18px;
			right: 15px;
			top: 15px;
		}
		&__answer {
			padding: 30px 0 0 10px;
		}
	}
}
