.product-card {
	padding: 15px 15px 20px 15px;
	background-color: rgba(247, 247, 247, 0.5);
	backdrop-filter: blur(21px);
	/* Note: backdrop-filter has minimal browser support */
	border-radius: 15px;

	&__img {
		// height: 250px;
		position: relative;
		margin-bottom: 20px;
		overflow: hidden;
		img {
			transition: 0.3s all;
			// height: 100%;
			width: 100%;
			// object-fit: cover;
		}
	}
	&__video-reviews {
		position: absolute;
		top: 15px;
		right: 0;
		padding: 11px 10px;
		color: $red;
		background-color: #fff;
		background: rgba(255, 255, 255, 0.8);
		backdrop-filter: blur(2px);
		border-radius: 15px 0 0 15px;
		text-transform: uppercase;
		font-weight: bold;
		font-size: 8px;
		line-height: 9px;
		text-transform: uppercase;
		text-decoration: none;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		svg {
			width: 14px;
			height: 10px;
			margin-left: 5px;
			color: $red;
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: scale(1.1);
			}
		}
	}
	&__title {
		@extend %card-title-1;
		font-weight: 700;
		color: $black-text;
		text-decoration: none;
		text-align: center;
		width: 100%;
		display: block;
		margin-bottom: 15px;
		transition: 0.3s all;
	}
	&__description {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
		margin-bottom: 10px;
	}
	&__key {
		width: 50%;
		font-weight: 300;
		font-size: 12px;
		line-height: 20px;
		font-family: $secondary-font;
		/* or 167% */
		color: #2c2c2c;
		margin-bottom: 5px;
	}
	&__value {
		width: 50%;
		text-align: right;
		font-family: $secondary-font;
		margin-bottom: 5px;
		font-weight: 300;
		font-size: 12px;
		line-height: 20px;
		/* or 167% */
	}
	&__bottom {
		padding-top: 15px;
		border-top: 0.5px solid #e9e9e9;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__price-uah {
		font-style: normal;
		font-weight: 600;
		font-size: 16px;
		line-height: 19px;
		// color: $red;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		&:after {
			content: '';
			width: 2px;
			height: 18px;
			background-color: $red;
			display: inline-block;
			margin: 0 6px;
		}
	}
	&__price-usd {
		font-weight: 700;
		font-size: 18px;
		line-height: 21px;
		// color: $red;
	}
	&__price {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		color: $red;
		font-feature-settings: 'pnum' on, 'lnum' on;
		&_grey {
			color: $grey;
			.product-card__price-uah:after {
				background-color: $grey;
			}
		}
	}
	&__details {
		font-family: $secondary-font;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-decoration: none;
		font-weight: 500;
		font-size: 12px;
		line-height: 20px;
		/* identical to box height, or 167% */
		text-align: right;
		text-transform: uppercase;
		color: $black-text;
		position: relative;
		transition: 0.3s all;
		z-index: 1;
		&::after {
			content: '';
			width: calc(100% + 10px);
			height: calc(100% + 6px);
			left: -3px;
			position: absolute;
			transition: 0.3s all;
			z-index: -1;
			border-radius: 15px;
		}
		svg {
			width: 20px;
			height: 13px;
			margin-left: 15px;
			transition: 0.3s all;
		}

		&:hover {
			color: #fff;
			&::after {
				background-color: $red;
			}
			svg {
				transform: translateX(5px);
			}
		}
	}
	.stickers {
		position: absolute;
		top: 15px;
		left: 0;
		transition: 0.3s all;
		&__item {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&_outstock {
		.product-card__img {
			img {
				opacity: 0.6;
			}
		}
	}
	&:hover {
		cursor: pointer;
		.stickers {
			transform: translateX(-100%);
		}
		.product-card__title {
			color: $red;
		}
		.product-card__img {
			img {
				transform: scale(1.1);
			}
		}
	}

	@include _mobile {
		padding: 10px;
		&__price {
			flex-wrap: wrap;
		}
		&__price-uah {
			font-weight: 600;
			font-size: 12px;
			line-height: 14px;
		}
		&__price-use {
			font-weight: 600;
			font-size: 14px;
			// line-height: 14px;
		}
		&__details svg {
			width: 16px;
			margin-left: 5px;
		}
		// &__bottom {
		// 	flex-wrap: wrap;
		// 	text-align: center;
		// }
	}
	@media (min-width: 1024px) and (max-width: 1199px) {
	}
}
