.reviews-carousel {
	position: relative;
	margin-bottom: $section-margin_pc;
	overflow: hidden;
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	.owl-nav {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 0;
		position: absolute;
		left: -70px;
		width: calc(100% + 140px);
		top: 40%;
	}

	.owl-stage-outer {
		padding: 0 10px;
		left: -10px;
		width: calc(100% + 20px);
	}
	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.section-title {
			font-size: 25px;
			margin-bottom: #{$section-title-margin_mobile - 20px};
		}
	}
	@include _tablet {
		margin-bottom: 70px;
		.section-title {
			margin-bottom: 50px;
		}
		.owl-dots {
			margin-top: 30px;
		}
	}
	// @media(min-width:)
}
