.footer {
	background-color: #fff;
	min-height: 265px;
	margin-left: 80px;
	position: relative;
	z-index: 2;
	&__top {
		background-color: $red;
		padding: 24px 0;
		position: relative;
		z-index: 3;
		// margin-left: 80px;
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}
	&__bottom {
		position: relative;
		background-color: #fff;

		&-wrapper {
			display: flex;
			padding: 34px 0;
			align-items: center;
			justify-content: space-between;
			border-bottom: 0.5px solid rgba(44, 44, 44, 0.3);
		}
	}
	&__services {
		@extend %ul-reset;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		// margin-right: 56px;
		li:first-child .footer__services-link {
			padding-left: 0;
		}
		li:last-child .footer__services-link {
			padding-right: 0;
		}
	}
	&__services-link {
		display: block;
		padding: 0 20px;
		@extend %footer-text;
		color: #fff;
		text-decoration: none;
		overflow: hidden;
		position: relative;
		z-index: 1;
		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			background-color: $black-text;
			position: absolute;
			transition: 0.3s all;
			top: 0;
			left: 0;
			transform: translateX(-100%);
			z-index: -1;
			opacity: 0;
		}
	}
	&__testdrive {
		@extend %footer-text;
		text-transform: uppercase;
		text-decoration: none;
		color: #fff;
		opacity: 0;
		visibility: hidden;
		display: none;
		// margin-right: 30px;
	}
	&__online {
		@extend %footer-text;
		text-transform: uppercase;
		text-decoration: none;
		color: #fff;
		padding: 16px 30px;
		border-radius: 25px;
		background: $black-text;
		box-shadow: 0px 3px 12px rgba(184, 33, 51, 0.4);
		transition: 0.3s all;
		opacity: 0;
		visibility: hidden;
		display: none;
		&:hover {
			color: $red;
		}
	}
	&__logo {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 155px;
		height: 63px;
		// margin-right: 60px;
		svg {
			width: 100%;
			height: 100%;
		}
	}
	&__menu {
		@extend %ul-reset;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		// margin-right: 70px;
		li {
			position: relative;
		}
		li:first-child .footer__menu-link {
			padding-left: 0;
		}
		li:last-child .footer__menu-link {
			padding-right: 0;
		}
	}
	&__menu-link {
		@extend %footer-text-2;
		color: $black-text;
		text-decoration: none;
		padding: 0 15px;
		transition: 0.3s all;
		&:hover {
			color: $red;
		}
	}
	&__menu-sticker {
		position: absolute;
	}
	&__contacts {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		// margin-right: 70px;
	}
	&__contacts-icon {
		width: 22px;
		height: 22px;
		margin-right: 15px;
		svg {
			width: 100%;
			height: 100%;
			color: $red;
		}
	}
	&__contacts-text {
		@extend %footer-text-contacts;
		font-feature-settings: 'pnum' on, 'lnum' on;
		text-transform: none;
		color: $black-text;
	}

	&__callback-href {
		display: block;
		@extend %footer-callback-href;
		text-decoration: none;
		margin-bottom: 10px;
		transition: 0.3s all;
		&:hover {
			color: $red;
		}
	}
	&__callback-btn {
		display: block;
		@extend %footer-callback-btn;
		transition: 0.3s all;
		&:hover {
			text-decoration: none;
			color: $black-text;
		}
	}

	&__copyright {
		padding: 20px 0;
		position: relative;
		background-color: #fff;
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}

	&__copyright-text {
		font-family: $secondary-font;
		font-weight: normal;
		font-size: 14px;
		line-height: 16px;
		color: rgba(44, 44, 44, 0.5);
	}
	&__dev {
		font-family: $secondary-font;
		font-weight: normal;
		font-size: 14px;
		line-height: 16px;
		text-align: right;
		color: rgba(44, 44, 44, 0.5);
	}

	@include _mobile-tablet {
		margin-left: 0;
		&__top {
			margin-left: 0;
			padding: 14px 0;
			.container {
				flex-wrap: wrap;
			}
		}
		&__services-link {
			padding: 5px 10px;
			color: $black-text;
		}
		&__services {
			flex-wrap: wrap;
			justify-content: center;
			margin-bottom: 30px;
		}
		&__menu {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 30px;
			flex-wrap: wrap;
			li {
				position: relative;
			}
			.menu__sticker {
				// position: absolute;
				// right: 30px;
				margin-right: 30px;
			}
		}
		&__bottom-wrapper {
			flex-wrap: wrap;
		}

		&__testdrive,
		&__online {
			opacity: 1;
			visibility: visible;
			display: block;
		}
	}

	@include _mobile {
		&__contacts {
			width: 50%;
			&-text {
				font-weight: 500;
				font-size: 12px;
				line-height: 14px;
				font-feature-settings: 'pnum' on, 'lnum' on;
			}
			&-icon {
				width: 18px;
				height: 18px;
			}
		}
		&__callbakc {
			width: 50%;
		}
		&__services {
			li {
				width: 50%;
			}
			&-link {
				text-align: center;
			}
		}
		&__menu {
			li {
				width: 50%;
			}
			&-link {
				display: block;
				padding: 0;
				width: 100%;
				text-align: center;
			}
		}
		&__copyright {
			.container {
				flex-wrap: wrap;
				justify-content: center;
				flex-direction: column-reverse;
			}
		}
		&__copyright-text {
			text-align: center;
			width: 100%;
		}
		&__dev {
			text-align: center;
			margin-bottom: 15px;
		}
	}

	@include _tablet {
		&__top {
			.container {
				justify-content: space-around;
			}
		}
	}

	@include _laptop-sm {
		margin-left: 60px;
		&__services {
			flex-wrap: wrap;
			max-width: 600px;
			li:first-child .footer__services-link {
				padding-left: 10px;
			}
			li:last-child .footer__services-link {
				padding-right: 10px;
			}
		}
		&__services-link {
			padding: 0 10px 5px 10px;
			&:after {
				top: -5px;
			}
		}
		&__bottom-wrapper {
			flex-wrap: wrap;
		}
		&__contacts,
		&__callback {
			width: 50%;
			text-align: center;
			justify-content: center;
		}
		&__callback-btn {
			text-align: center;
		}
	}
	@include _laptop {
		margin-left: 60px;
		&__services-link {
			padding: 0 10px;
		}
	}
	@media (min-width: 1024px) {
		&__services-link {
			&:hover {
				&:after {
					opacity: 1;
					transform: translateX(0);
				}
			}
		}
	}
}
