.brands {
	margin-bottom: $section-margin_pc;
	position: relative;
	overflow: hidden;
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		// height: 100%;
		height: 80px;
		width: 80px;
		img {
			width: auto;
			height: 100%;
			object-fit: contain;
		}
	}
	.owl-carousel .owl-stage {
		display: flex;
	}
	.owl-nav {
		position: absolute;
		display: flex;
		align-items: center;
		top: -74px;
		right: 0;
		button {
			margin-right: 20px;
			&:last-child {
				margin-right: 0;
			}
		}
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.owl-dots {
			margin-top: 20px;
		}
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
	}

	@include _tablet {
		margin-bottom: 100px;
		.owl-dots {
			margin-top: 20px;
		}
	}
}
