.quantity {
	max-width: 100px;
	display: flex;
	align-items: center;
	border: 0.5px solid rgba(44, 44, 44, 0.5);
	box-sizing: border-box;
	border-radius: 25px;
	background-color: #fff;
	min-height: 37px;
	input {
		width: 100%;
		background-color: transparent;
		border: none;
		height: 37px;
		text-align: center;
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		/* identical to box height */
		color: $black-text;
	}
	button {
		background-color: transparent;
		border: none;
		height: 37px;
		cursor: pointer;
		color: $grey;
		width: 27px;
		min-width: 27px;
		transition: 0.3s all;
		padding: 0;
		font-size: 27px;
		line-height: 1;
		&:hover {
			color: $red;
		}
	}
}
