.price {
	position: relative;
	margin-bottom: $section-margin_pc;
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
	}
}

.price-table {
	width: 100%;
	display: flex;
	align-items: stretch;
	justify-content: flex-start;
	flex-wrap: wrap;
	border: 1px solid $grey;
	border-radius: 15px;
	background-color: #fff;
	&__key {
		max-width: 1010px;
		width: 100%;
		padding: 20px 30px;
		border-bottom: 1px solid $grey;
		font-weight: 600;
		font-size: 20px;
		line-height: 24px;
		/* identical to box height, or 120% */
		color: $black-text;
		&_no-border {
			border-bottom: none;
		}
	}
	&__value {
		padding: 20px 30px;
		border-bottom: 1px solid $grey;
		max-width: 308px;
		width: 100%;
		font-weight: 800;
		font-size: 18px;
		line-height: 24px;
		/* identical to box height, or 133% */
		border-left: 1px solid $grey;
		color: $red;
		span:last-child {
			&::before {
				content: '/';
				margin: 0 3px;
				// width: 1px;
				display: inline-block;
				// height: 16px;
				// background-color: $red;
			}
		}
		&:last-child {
			border-bottom: none;
		}
	}

	@include _mobile {
		&__key {
			font-size: 12px;
			line-height: 12px;
			width: 65%;
			max-width: none;
			padding: 15px 10px;
			align-items: center;
			justify-content: flex-start;
			display: flex;
		}
		&__value {
			font-weight: 800;
			font-size: 12px;
			line-height: 16px;
			width: 35%;
			max-width: none;
			padding: 15px 10px;
		}
	}
	@include _tablet {
		&__key {
			width: 65%;
			max-width: none;
		}
		&__value {
			width: 35%;
			max-width: none;
		}
	}
}
