.input {
	position: relative;
	z-index: 1;
	input,
	textarea {
		width: 100%;
		background-color: transparent;
		position: relative;
		z-index: 1;
		padding: 17px 24px;
		border-radius: 25px;
		outline: none;
		resize: none;
		@extend %input-text;
		/* identical to box height, or 100% */
	}
	&_red input {
		border: 1px solid $red;
		color: #fff;
	}

	&_gradient {
		$border: 1px;
		background-color: #fff;
		background-clip: padding-box;
		border: solid $border transparent;
		border-radius: 25px;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		&::before {
			content: '';
			display: flex;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			// z-index: -1;
			border-radius: inherit;
			margin: -$border;
			background: linear-gradient(268.43deg, #b72032 9.01%, rgba(183, 32, 50, 0) 100%);
		}
		input,
		textarea {
			background-color: #fff;
			border: none;
			&::placeholder {
				color: #b3b3b3;
			}
		}
	}
	&_bottom-border {
		border-radius: 0;
		input {
			border: none;
			border-radius: 0;
			border-bottom: 1px solid $border-color;
			font-size: 16px;
			padding: 10px 20px;
			font-weight: 500;
		}
	}

	&__placeholder {
		position: absolute;
		z-index: 1;
		cursor: text;
		top: 17px;
		left: 20px;
		width: auto;
		color: #b3b3b3;
		&::after {
			content: ' *';
			font-size: 16px;
			color: $red;
		}
	}
	@include _mobile {
		&_gradient {
			input,
			textarea {
				padding: 10px 15px;
			}
			.input__placeholder {
				top: 10px;
			}
		}
	}
	@include _tablet {
		&_gradient {
			input,
			textarea {
				padding: 12px 18px;
			}
			.input__placeholder {
				top: 12px;
			}
		}
	}
}
// checkbox

.checkbox {
	display: block;
	position: relative;
	cursor: pointer;
	display: flex;
	&__caption {
		font-weight: normal;
		font-size: 16px;
		line-height: 16px;
		font-feature-settings: 'pnum' on, 'lnum' on;
		/* identical to box height, or 100% */
		color: $black-text;
		transition: 0.3s all;
	}
	input {
		display: none;
		opacity: 0;
		visibility: hidden;
		&:checked ~ .checkmark {
			background-color: $red;
			border-color: $red;
			&::after {
				opacity: 1;
				visibility: visible;
			}
		}
		&:checked ~ .checkbox__caption {
			color: $red;
			font-weight: 600;
		}
	}
	.checkmark {
		display: block;
		width: 12px;
		min-width: 12px;
		height: 12px;
		border-radius: 50%;
		border: 1px solid rgba(179, 179, 179, 0.5);
		transition: 0.3s all;
		margin-right: 15px;
		position: relative;
		&::after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			background-image: url(../img/check.svg);
			background-repeat: no-repeat;
			background-position: center center;
			box-sizing: 10px 10px;
			opacity: 0;
			visibility: hidden;
			transition: 0.3s all;
		}
	}

	&_big {
		width: fit-content;
		.checkmark {
			width: 32px;
			height: 32px;
			border: 1px solid rgba(179, 179, 179, 0.5);
			&:after {
				content: none;
			}
		}

		input:checked ~ .checkmark {
			background-color: inherit;
			border: 3px solid $red;
		}
	}
}

// select 2
.select2 {
	padding: 15px 30px;
	$border: 1px;
	background-color: #fff;
	background-clip: padding-box;
	border: solid $border transparent;
	border-radius: 25px;
	position: relative;
	&::before {
		content: '';
		display: flex;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: -1;
		border-radius: inherit;
		margin: -$border;
		background: linear-gradient(60deg, rgba(255, 255, 255, 0.3) 5%, rgba(183, 32, 50, 1) 30%);
	}
	// set height for all select
	&-selection {
		height: auto !important;
	}
	// delete default border
	&-selection--single {
		border: none !important;
	}
	// text for select
	&-selection__rendered {
		@extend %input-text;
		line-height: 19px !important;
	}
	// select arrow
	&-selection__arrow {
		b {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
		background-position: center center;
		background-repeat: no-repeat;
		background-size: 100% 100%;
		background-image: url(../img/arrow-black.svg);
		width: 16px;
		top: 13px !important;
		right: 15px !important;
		transform: rotate(-90deg);
		transition: 0.3s all;
	}
}

.select2-container--open .select2-selection__arrow {
	transform: rotate(90deg) !important;
}

.select__dropdown {
	padding: 10px 0;
	$border: 1px;
	background-color: #fff;
	background-clip: padding-box;
	border: 1px solid $red;
	border-top: 1px solid $red !important;
	border-radius: 25px !important;
	position: relative;
	overflow: hidden;
	z-index: 1;
}
.select2-results__options {
	&::-webkit-scrollbar-track {
		background-color: #e5e5e5;
		border-radius: 5px;
	}
	&::-webkit-scrollbar-thumb {
		border-radius: 3px;
		background-color: $red;
	}

	&::-webkit-scrollbar {
		border-radius: 5px;
		width: 5px;
	}
}
