.stickers {
	&__item {
		@extend %products-stickers;
		padding: 9px 10px;
		border-radius: 0 15px 15px 0;
		width: fit-content;
		&_red {
			background-color: $red;
		}
		&_orange {
			background-color: $orange;
		}
		&_grey {
			background-color: $grey;
		}
		&_black {
			background-color: $black-text;
		}
		&_blue {
			background-color: #7294c7;
		}
	}
}
