.pagination {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: center;

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 11px;
		@extend %pagination-text;
		width: 44px;
		height: 44px;
		display: flex;
		align-items: center;
		justify-content: center;
		text-decoration: none;
		border-radius: 50%;
		transition: 0.3s all;
		svg {
			width: 22px;
			height: 22px;
			color: $black-text;
		}

		&:hover {
			background-color: $red;
			color: #fff;
			svg {
				color: #fff;
			}
		}
	}
	&__item-prev {
		margin-right: 30px;
		svg {
			transform: rotate(180deg);
		}
	}
	&__item-next {
		margin-left: 30px;
	}
	&__item-prev,
	&__item-next {
		width: 44px;
		height: 44px;
		border-radius: 50%;
		background-color: rgba(179, 179, 179, 0.22);
	}
	.active {
		@extend %pagination-text;
		font-weight: 800;
		background-color: $red;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 44px;
		height: 44px;
		color: #fff;
	}
	@include _mobile {
		// flex-wrap: wrap;
		max-width: 280px;
		margin-left: auto;
		margin-right: auto;
		&__item {
			width: 32px;
			height: 32px;
			padding: 0;
		}
		&__item-prev {
			margin-right: 5px;
		}
		&__item-next {
			margin-left: 5px;
		}
		.active {
			width: 32px;
			height: 32px;
		}
	}
}
