.cart {
	margin-bottom: 100px;
	&__section {
		margin-bottom: 60px;
	}
	&__section-heading {
		width: 100%;
		padding: 22px;
		font-family: $secondary-font;
		font-weight: 500;
		font-size: 16px;
		line-height: 18px;
		margin-bottom: 40px;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		color: #ffffff;
		background: #2c2c2c;
		border-radius: 15px;
	}

	.input {
		margin-bottom: 25px;
	}
	.checkbox {
		margin-bottom: 20px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	.select2 {
		margin-bottom: 25px;
	}

	.input_margin {
		margin-bottom: 30px;
	}

	&__total {
		padding-bottom: 30px;
		margin-bottom: 30px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-bottom: 1px solid rgba(44, 44, 44, 0.5);
		b {
			font-weight: 700;
		}
	}
	&__total-caption {
		font-family: $secondary-font;
		font-weight: 900;
		font-size: 18px;
		line-height: 18px;
		/* or 100% */
		letter-spacing: 0.1em;
		text-transform: uppercase;
		color: $black-text;
	}
	&__total-price {
		font-weight: 600;
		font-size: 18px;
		line-height: 21px;
		text-align: right;
		text-transform: uppercase;
		color: $red;
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
	}
	&__actions {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__actions-back {
		font-weight: 500;
		font-size: 16px;
		line-height: 19px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		/* identical to box height */
		text-transform: uppercase;
		color: #000000;
		text-decoration: none;
		&::before {
			content: '';
			display: inline-block;
			width: 20px;
			min-width: 20px;
			height: 20px;
			background-repeat: no-repeat;
			background-position: center center;
			background-image: url('../img/arrow-black.svg');
			transition: 0.3s all;
		}
		&:hover {
			&::before {
				transform: translateX(-5px);
			}
		}
	}
	@include _mobile {
		margin-bottom: 50px;
		&__section-heading {
			padding: 15px;
			margin-bottom: 20px;
			&_first {
				opacity: 0;
				visibility: hidden;
				display: none;
			}
		}
		&__section-caption {
			font-size: 12px;
			margin-bottom: 15px;
			text-align: center;
			&_no-margin {
				margin-bottom: 0;
			}
		}
		.input {
			margin-bottom: 10px;
		}
		&__section {
			margin-bottom: 40px;
		}
		&__actions {
			flex-direction: column-reverse;
		}
		.btn-primary {
			margin-bottom: 20px;
		}
		&__total-price {
			justify-content: flex-end;
		}
	}
	@include _tablet {
		&__section-heading {
			margin-bottom: 20px;
			padding: 10px 30px;
			font-size: 14px;
		}

		&__section {
			margin-bottom: 20px;
		}
		.input {
			margin-bottom: 15px;
		}
	}
}
