.leave-review {
	margin-bottom: $section-margin_pc;
	.section-title {
		margin-bottom: $section-title-margin_pc;
		padding-left: 0;
	}
	.section-uptitle {
		padding-left: 0;
	}
	.rating-wrapper {
		margin-left: 80px;
	}
	&__form {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-wrap: wrap;
	}
	.input {
		max-width: 640px;
		width: 100%;
		&_big {
			max-width: 980px;
			margin-top: 25px;
			margin-bottom: 30px;
		}
		&__placeholder {
			font-family: $secondary-font;
			font-weight: 300;
			font-size: 14px;
			line-height: 14px;
		}
	}

	@include _laptop-sm {
		.rating-wrapper {
			margin-left: auto;
		}
	}
	@include _mobile-tablet {
		.rating-wrapper {
			margin-left: auto;
		}
		.input {
			max-width: none;
			width: 50%;
			&_big {
				width: 100%;
			}
		}
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
		.rating-wrapper {
			margin-left: 10px;
			margin-top: 20px;
		}
		.input {
			width: 100%;
		}
	}
}
