.logo {
	display: flex;
	align-items: center;
	justify-content: center;
	svg {
		width: 100%;
		height: 100%;
	}
	&_header {
		width: 155px;
		min-width: 155px;
		height: 63px;
	}
	&_mobile {
		align-items: center;
		justify-content: center;
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	&_footer {
		width: 155px;
		height: 63px;
	}

	@include _mobile {
		&_header {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
		&_mobile {
			width: 78px;
			height: 78px;
			display: flex;
			opacity: 1;
			visibility: visible;
			position: relative;
			top: 10px;
			margin-right: 22px;
		}
		&_footer {
			margin-left: auto;
			margin-right: auto;
			width: 142px;
			height: 57px;
			margin-bottom: 30px;
		}
	}
	@include _tablet {
		&_header {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
		&_mobile {
			width: 100px;
			min-width: 100px;
			height: 100px;
			opacity: 1;
			display: flex;
			visibility: visible;
			position: relative;
			top: 20px;
			margin-right: 44px;
		}
		&_footer {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 20px;
		}
	}
}
