// first btn animation
@keyframes hoverBtnLoad {
	0% {
		transform: translateX(-100%);
	}
	50% {
		transform: translateX(0);
		box-shadow: 0px 5px 12px $orange;
	}
	100% {
		transform: translateX(100%);
	}
}
// fadeIn
@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
// SlideUp
@keyframes slideUp {
	from {
		opacity: 0;
		transform: translateY(100%);
	}
	to {
		opacity: 1;
		transform: translateY(0);
	}
}
// Hover redBtn
@keyframes hoverBtn {
	0% {
		transform: translateX(-100%);
	}
	50% {
		transform: translateX(0);
		box-shadow: 0px 5px 12px $orange;
	}
	100% {
		transform: translateX(100%);
	}
}
// pulse
@keyframes pulse {
	0% {
		transform: scale(1);
		opacity: 0;
	}
	50% {
		transform: scale(1.3);
		opacity: 0.8;
	}
	100% {
		transform: scale(1.4);
		opacity: 0;
	}
}
