.breadcrumbs {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 50px;
	li,
	li a {
		@extend %breadcrumbs-text;
		text-decoration: none;
		color: #fff;
		position: relative;
		overflow: hidden;
	}
	a::after {
		content: '';
		width: 100%;
		position: absolute;
		background-color: $black-text;
		transition: 0.3s all;
		opacity: 0;
		top: 0;
		left: 0;
		height: calc(100% + 6px);
		z-index: -1;
		transform: translateX(-100%);
	}
	.slash {
		margin: 0 20px;
	}
	.active {
		color: $red;
	}

	a:hover {
		&:after {
			opacity: 1;
			transform: translateX(0);
		}
	}
	@include _mobile-tablet {
		overflow-y: hidden;
		margin-bottom: 40px;
		overflow-x: auto;
		white-space: nowrap;
		height: 100%;
		// width: 100%;
		li a,
		li {
			overflow: visible;
		}
	}
}
