.reviews-section {
	position: relative;
	margin-bottom: $section-margin_pc;
	overflow: hidden;
	.section-uptitle {
		margin-bottom: 15px;
	}
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	.owl-nav {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		position: absolute;
		right: 0;
		top: -85px;
		button {
			margin-right: 20px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	.owl-dots {
		margin-top: 40px;
		// margin-bottom: ;
	}
	.owl-carousel {
		margin-bottom: 70px;
		&:last-child {
			margin-bottom: 0;
		}
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.owl-carousel {
			margin-bottom: 60px;
		}
		.owl-dots {
			margin-top: 20px;
		}
		.section-uptitle {
			margin-bottom: 10px;
		}
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
	}

	@include _tablet {
		margin-bottom: 100px;
		.reviews-card,
		.videoreviews-card {
			max-width: 700px;
			margin-left: auto;
			margin-right: auto;
		}
	}
}
