.product-page {
	&__wrapper {
		background: rgba(247, 247, 247, 0.5);
		// backdrop-filter: blur(15.3333px);
		/* Note: backdrop-filter has minimal browser support */
		border-radius: 15px;
		padding: 30px;
		margin-bottom: 30px;
	}
	&__heading {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 40px;
	}
	.stars {
		&__caption {
			margin-left: 20px;
			font-family: $secondary-font;
			font-weight: 300;
			font-size: 16px;
			line-height: 1.5;
			/* identical to box height, or 150% */
			text-decoration: none;
			color: $black-text;
			&:hover {
				text-decoration: underline;
			}
		}
	}
	&__status {
		margin-left: auto;
		font-family: $secondary-font;
		font-weight: 300;
		font-size: 16px;
		line-height: 1.5;
		color: $black-text;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		&::before {
			content: '';
			width: 12px;
			height: 12px;
			border-radius: 50%;
			display: inline-flex;
			margin-right: 15px;
		}
		&_green::before {
			background-color: $green;
		}
		&_blue::before {
			background-color: $blue;
		}

		&_black::before {
			background-color: $black-text;
		}
	}

	&__actions {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 30px;
		.btn-like {
			margin-left: 32px;
		}
	}

	&__video-reviews {
		padding: 30px 47px;
		min-height: 200px;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 100% 100%;
		border-radius: 15px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 120px;
		text-decoration: none;
		transition: 0.3s all;
		.btn-play {
			margin-left: 0;
			margin-right: 30px;
			svg {
				transition: 0.3s all;
			}
			&_red {
				svg {
					color: $red;
				}
			}
			// &:hover {
			// }
		}
		.card-uptitle {
			font-family: $secondary-font;
			font-weight: 900;
			font-size: 22px;
			line-height: 50px;
			/* identical to box height, or 227% */
			text-transform: uppercase;
			color: $black-text;
			&_white {
				color: #ffffff;
			}
		}
		.card-title {
			font-family: $secondary-font;
			font-weight: 900;
			font-size: 44px;
			line-height: 50px;
			letter-spacing: 0.1em;
			text-transform: uppercase;
			-webkit-text-fill-color: transparent;
			-webkit-text-stroke: 1px $red;
		}
	}
	&__description {
		margin-bottom: 120px;
		.section-title-3 {
			margin-bottom: 40px;
		}
		p {
			font-family: $secondary-font;
			font-weight: normal;
			font-size: 18px;
			line-height: 24px;
			color: $black-text;
		}
	}
	@include _mobile {
		&__heading {
			margin-bottom: 25px;
		}
		&__wrapper {
			padding: 15px;
			margin-bottom: 40px;
		}
		&__status {
			font-size: 12px;
			line-height: 14px;
			&::before {
				width: 8px;
				height: 8px;
			}
		}
		.stars {
			&__caption {
				font-size: 12px;
				line-height: 12px;
				margin-left: 10px;
			}
		}
		&__video-reviews {
			flex-direction: column;
			margin-bottom: 30px;
			min-height: 260px;
			background-size: cover;
			align-items: center;
			justify-content: center;
			&_last {
				margin-bottom: 60px;
			}
			.card-title {
				font-size: 30px;
				line-height: 35px;
			}
			.card-uptitle {
				font-size: 18px;
				line-height: 21px;
				text-align: center;
			}
			.btn-play {
				margin-right: 0;
				svg {
					width: 60px;
					height: 40px;
				}
			}
		}
		&__actions {
			.btn-like {
				margin-left: 20px;
				min-width: 24px;
			}
		}
		&__description {
			p {
				font-size: 16px;
				line-height: 22px;
			}
		}
	}
	@include _tablet {
		&__video-reviews {
			margin-bottom: 40px;
			background-size: cover;
			align-items: center;
			justify-content: flex-start;
		}
	}

	@include _laptop-sm {
		&__video-reviews {
			background-size: cover;
		}
	}
	@media (min-width: 1200px) {
		&__video-reviews {
			&:hover .btn-play svg {
				transform: scale(1.1);
			}
		}
	}
}

.product-page-price {
	padding-bottom: 30px;
	border-bottom: 1px solid $grey2;
	margin-bottom: 30px;
	&__value {
		font-style: normal;
		font-weight: 600;
		font-size: 24px;
		line-height: 28px;
		margin-bottom: 20px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-wrap: wrap;
		b {
			font-weight: 700;
		}
		&_red {
			color: $red;
		}
	}

	&__caption {
		font-family: $secondary-font;
		font-weight: 500;
		font-size: 14px;
		line-height: 24px;
		color: $black-text;
	}

	@include _mobile {
		&__value {
			font-size: 14px;
			line-height: 16px;
			margin-bottom: 15px;
		}
		&__caption {
			font-size: 12px;
			line-height: 14px;
		}
	}
}

.product-page-characteristics {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	flex-wrap: wrap;
	font-family: $secondary-font;
	font-weight: 300;
	font-size: 16px;
	line-height: 1.5;
	/* or 150% */
	color: $black-text;
	margin-bottom: 25px;
	b {
		font-weight: bold;
	}
	&__key {
		width: 50%;
	}
	&__value {
		text-align: right;
		width: 50%;
	}
	@include _mobile {
		font-size: 12px;
		line-height: 15px;
		margin-bottom: 30px;
	}
}

.product-page-request {
	padding-bottom: 30px;
	border-bottom: 1px solid $grey2;
	margin-bottom: 30px;
	&__caption {
		font-weight: bold;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		font-size: 16px;
		line-height: 24px;
		color: $black-text;
		margin-bottom: 15px;
	}
	&_accessories {
		padding-bottom: 0;
		margin-bottom: 0;
		border-bottom: none;
	}
	.btn-primary {
		width: 100%;
		min-width: auto;
		padding: 15px 6px;
		font-size: 12px;
	}
	@include _mobile {
		margin-bottom: 15px;
		&__caption {
			font-size: 14px;
			line-height: 14px;
			text-align: center;
			.tooltip span {
				right: 0;
				left: auto;
				transform: translateX(0);
				&::before {
					right: 5px;
					left: auto;
					transform: translateX(0);
				}
			}
		}
		.input {
			margin-bottom: 15px;
		}
		.btn-primary {
			padding: 7px 6px;
		}
	}
	@include _tablet {
		.btn-primary {
			padding: 10px 18px;
		}
	}
	@include _laptop-sm {
		&__caption {
			text-align: center;
		}
	}
}

.product-page-notice {
	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 15px;
		&_last {
			margin-bottom: 0;
		}
	}
	&__icon {
		width: 24px;
		min-width: 24px;
		height: 24px;
		margin-right: 15px;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 100%;
			height: 100%;
			color: $red;
		}
	}
	&__text {
		// font-weight: bold;
		font-size: 14px;
		line-height: 20px;
		/* or 143% */
		color: $black-text;
	}

	.download {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		width: fit-content;
		&__text {
			font-weight: 600;
			font-size: 16px;
			line-height: 19px;
			text-decoration-line: underline;
			text-transform: uppercase;
			color: $red;
			margin-right: 10px;
		}
		&__icon {
			width: 20px;
			height: 20px;
			display: flex;
			align-items: center;
			justify-content: center;
			svg {
				width: 100%;
				height: 100%;
				fill: transparent;
				color: $red;
			}
		}
		&:hover {
			.download__text {
				text-decoration: none;
			}
			text-decoration: none;
		}
	}
	@include _mobile {
		&__text {
			font-size: 12px;
			line-height: 14px;
		}
		.download {
			&__text {
				font-size: 11px;
			}
		}
	}
	@include _laptop-sm {
		.download {
			margin-left: auto;
			margin-right: auto;
		}
	}
	@include _laptop {
		.download {
			&__text {
				font-size: 12px;
				// margin-right: 5px;
			}
			&__icon {
				width: 15px;
				height: 15px;
			}
		}
	}
}
