.contacts {
	margin-bottom: $section-margin_pc;
	.section-uptitle {
		margin-bottom: 25px;
		text-transform: none;
		padding-left: 0;
		font-size: 32px;
	}
	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		// margin-bottom: 40px;
	}
	&__img {
		border-radius: 15px 0px 0px 15px;
		position: relative;
		overflow: hidden;
		&::after {
			content: '';
			top: 0;
			left: 0;
			position: absolute;
			width: 100%;
			height: 100%;
			background: rgba(44, 44, 44, 0.2);
		}
		img {
			width: 100%;
		}
	}
	&__map {
		height: 470px;
		border-radius: 0px 15px 15px 0px;
		overflow: hidden;
	}
	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.section-uptitle {
			font-size: 22px;
		}
		&__wrapper {
			flex-direction: column;
			align-items: flex-start;
			order: 2;
		}

		&__img {
			max-height: 300px;
			max-width: 370px;
			border-radius: 15px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 20px;
			img {
				// object-fit: contain;
			}
		}
		&__map {
			height: 300px;
			max-width: 370px;
			margin-left: auto;
			margin-right: auto;
			iframe {
				height: 100%;
			}
		}
	}
	@include _tablet {
		.section-uptitle {
			font-size: 26px;
		}
		&__img {
			height: 100%;
			img {
				height: 100%;
				object-fit: cover;
			}
		}
		&__map {
			height: 100%;
			iframe {
				height: 100%;
			}
		}
	}
	@include _laptop-sm {
		margin-bottom: 80px;
		&__map {
			height: 100%;
			iframe {
				height: 100%;
			}
		}
	}

	@include _laptop {
		&__img {
			height: 100%;
			img {
				height: 100%;
				object-fit: cover;
			}
		}
	}
}

.contacts {
	&__social {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-right: 50px;
		margin-bottom: 40px;
	}
	&__social-item {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 50px;
		height: 20px;
		padding: 0 15px;
		border-right: 1px solid rgba(179, 179, 179, 0.22);

		&:first-child {
			padding-left: 0;
		}
		&:last-child {
			padding-right: 0;
			border-right: none;
		}
		svg {
			color: $red;
			width: 20px;
			min-width: 20px;
			height: 20px;
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: scale(1.2);
			}
		}
	}
	@include _mobile {
		&__social {
			margin-right: 0;
		}
	}
}

.contacts {
	&__item {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		margin-bottom: 30px;
		text-decoration: none;
		&:last-child {
			margin-bottom: 70px;
		}
	}
	&__item-icon {
		width: 20px;
		min-width: 20px;
		height: 20px;
		margin-right: 15px;
		svg {
			width: 100%;
			height: 100%;
			color: $red;
		}
	}
	&__item-text {
		@extend %card-title-4;
		font-weight: 500;
		transition: 0.3s all;
		span {
			display: block;
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		&_bold {
			font-weight: bold;
		}
	}
	a:hover {
		.contacts__item-text {
			color: $red;
		}
	}

	@include _mobile {
		&__item {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 20px;
			}
		}
	}
}
