.product-page-gallery {
	&__big {
		position: relative;
		border: 1px solid rgba(179, 179, 179, 0.22);
		border-radius: 15px;
		overflow: hidden;
		margin-bottom: 30px;
		.stickers {
			position: absolute;
			top: 32px;
			left: 0;
			z-index: 2;
			&__item {
				font-size: 16px;
				line-height: 19px;
				padding: 12px 20px;
				border-radius: 0 17px 17px 0;
				margin-bottom: 14px;
			}
			&_beige,
			&_blue,
			&_black {
				text-align: center;
				font-weight: bold;
				font-size: 18px;
				line-height: 21px;
				text-transform: uppercase;
				color: #ffffff;
				width: 100%;
				padding: 15px 10px 10px 10px;
				border-radius: 15px 15px 0 0;
				top: 0;
				left: 0;
			}
			&_blue {
				background-color: rgba(114, 148, 199, 0.9);
			}
			&_beige-bottom,
			&_blue-bottom,
			&_black-bottom {
				bottom: 0;
				top: auto;
				border-radius: 0 0 15px 15px;
			}
			&_beige {
				background-color: rgba(179, 179, 179, 0.9);
			}
			&_black {
				background-color: rgba(44, 44, 44, 0.9);
			}
		}
		.owl-nav {
			position: absolute;
			z-index: 2;
			top: 50%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			height: 0;
			left: 20px;
			width: calc(100% - 40px);
			button {
				background-color: rgba(179, 179, 179, 0.5) !important;
				transition: 0.3s all;
				svg {
					color: $black-text;
					transition: 0.3s all;
				}
				&:hover {
					background-color: $red !important;
					svg {
						color: #fff;
					}
				}
			}
		}
	}
	&__big-item {
		max-width: 760px;
		height: auto;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__thumbs {
		margin: 0 13px;
		display: flex;
	}
	&__thumbs-item {
		width: 160px;
		height: 160px;
		cursor: pointer;
		overflow: hidden;
	}
	#thumbs,
	#thumbs2 {
		.product-page-gallery__thumbs-item img {
			border-radius: 15px;
			border: 1px solid transparent;
			transition: 0.3s all;
			object-fit: cover;
		}
		.current .product-page-gallery__thumbs-item img {
			border-color: $red;
		}
	}

	@include _mobile {
		&__big {
			max-width: 420px;
			margin-left: auto;
			margin-right: auto;
			border: none;
			.stickers {
				top: 10px;
				&__item {
					font-size: 12px;
					line-height: 14px;
					padding: 7px 5px;
					margin-bottom: 5px;
				}

				&_beige,
				&_blue,
				&_black {
					top: 0;
					padding: 3px 10px;
					font-size: 14px;
				}
				&_beige-bottom,
				&_blue-bottom,
				&_black-bottom {
					top: auto;
					bottom: 30px;
				}
			}
		}
		&__big-item {
			display: flex;
			align-items: center;
			justify-content: center;
			max-width: 420px;
			width: 100%;
			height: auto;
			border: 1px solid $border-color !important;
			border-radius: 15px;
			overflow: hidden;
			img {
				width: 100% !important;
				height: 100% !important;
				object-fit: cover;
			}
		}
		&__thumbs {
			opacity: 0;
			visibility: hidden;
			display: none !important;
		}
		.owl-dots {
			margin-top: 20px;
			.owl-dot {
				width: 10px;
				height: 10px;
			}
		}
		&_row {
		}
	}
	@include _tablet {
		&__big {
			border: none;
			max-width: 600px;
			margin-left: auto;
			margin-right: auto;

			.stickers {
				&_beige-bottom,
				&_blue-bottom,
				&_black-bottom {
					bottom: 30px;
					top: auto;
					border-radius: 0 0 15px 15px;
				}
			}
		}
		&__big-item {
			display: flex;
			align-items: center;
			justify-content: center;
			max-width: 600px;
			width: 100%;
			height: auto;
			border: 1px solid $border-color !important;
			border-radius: 15px;
			overflow: hidden;
			img {
				width: 100% !important;
				height: 100% !important;
			}
		}
		&__thumbs {
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			display: none !important;
		}
		.owl-dots {
			margin-top: 20px;
			.owl-dot {
				width: 10px;
				height: 10px;
			}
		}
	}
	@include _laptop-sm {
		&__big {
			max-width: 720px;
			height: auto;
			margin-left: auto;
			margin-right: auto;
		}
		&__big-item {
			img {
				width: 100% !important;
			}
		}

		&__thumbs {
			margin-bottom: 30px;
		}
		#big2 {
			.owl-item img {
				width: auto !important;
			}
		}
	}
	@include _laptop {
		&__thumbs-item {
			width: 130px;
			height: 130%;
		}
		#big2 {
			.owl-item img {
				width: auto !important;
			}
		}

		&__big-item {
			width: auto;
			height: 100%;
		}
	}

	@media (min-width: 1600px) {
		position: sticky;
		top: 160px;
		margin-bottom: 30px;
		&__big {
			&_sm {
				width: 563px;
				height: 563px;
				margin-left: auto;
				.product-page-gallery__big-item {
					border-radius: 14px;
					overflow: hidden;
					width: 563px;
					height: 563px;
					img {
						height: 100%;
						border-radius: 15px;
						object-fit: cover;
					}
				}
			}
		}
		&_row {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			flex-direction: row-reverse;
			margin-bottom: 120px;
			#thumbs2 {
				transform: rotate(90deg);
				max-width: 540px;
				position: absolute;
				transform-origin: top left;
				top: 0;
				left: 162px;
				.product-page-gallery__thumbs-item {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 158px;
					height: 158px;
					img {
						width: 156px;
						height: 156px;
						border-radius: 15px;
						transform: rotate(-90deg);
						border: 1px solid rgba(179, 179, 179, 0.22);
						transition: 0.3s all;
					}
				}
			}
			// flex-wrap: wrap;
		}
	}
}
