.tooltip {
	margin-left: 10px;
	position: relative;
	z-index: 19;
	height: 20px;
	width: 20px;
	min-width: 20px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	opacity: 1;
	width: fit-content;
	visibility: visible;
	svg {
		width: 12px;
		height: 12px;
	}
	span {
		position: absolute;
		padding: 10px;
		opacity: 0;
		left: 50%;
		transform: translateX(-50%);
		top: 150%;
		font-size: 12px;
		font-weight: normal;
		line-height: 1.2;
		visibility: hidden;
		text-transform: none;
		transition: 0.3s all;
		background-color: #fff;
		border-radius: 5px;
		color: $black;
		z-index: 19;
		width: 300px;
		&::before {
			content: '';
			position: absolute;
			bottom: 100%;
			left: 50%;
			transform: translateX(-50%);
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 0 5px 5px 5px;
			border-color: transparent transparent #ffffff transparent;
		}
	}
	@include _mobile-tablet {
		span {
			width: 200px;
		}
		.visible {
			opacity: 1;
			visibility: visible;
		}
	}
	@media (min-width: 1024px) {
		&:hover {
			span {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	@media (min-width: 1200px) and (max-width: 1300px) {
		span {
			left: 30%;
			transform: translateX(-70%);
			&::before {
				left: 70%;
				transform: translateX(-30%);
			}
		}
	}
}
