.reviews {
	position: relative;
	margin-bottom: $section-margin_pc;
	overflow: hidden;
	.section-title {
		margin-bottom: $section-title-margin_pc;
	}
	.reviews-card {
		margin-bottom: 30px;
		min-height: 278px;
	}

	.pagination {
		margin-top: 40px;
	}

	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.reviews-card,
		.videoreviews-card {
			// height: 100%;
			max-height: none;
			margin-bottom: 20px;
		}
		.section-title {
			margin-bottom: $section-title-margin_mobile;
		}
	}
}
