.btn {
	&-primary {
		@extend %btn-text;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		cursor: pointer;
		background-color: transparent;
		text-decoration: none;
		text-align: center;
	}
	&-size-1 {
		padding: 15px 10px;
		width: fit-content;
		min-width: 263px;
		border-radius: 25px;
	}
	&-size-2 {
		padding: 15px 10px;
		border-radius: 25px;
		min-width: 190px;
		height: fit-content;
		width: fit-content;
		@include _mobile {
			border-radius: 14px;
		}
	}
	&-size-3 {
		padding: 17px 20px;
		min-width: 315px;
		width: fit-content;
		border-radius: 25px;
		@include _mobile {
			padding: 7px 40px;
			border-radius: 14px;
		}
	}
	&-size-4 {
		padding: 15px 20px;
		width: 100%;
		border-radius: 25px;
	}
	&-size-5 {
		padding: 15px 20px;
		min-width: 414px;
		border-radius: 25px;
		width: fit-content;
	}
	&-border {
		border: 2px solid $red;
		color: $black-text;
		transition: 0.3s all;
		&:hover {
			background-color: $red;
			color: #fff;
		}
	}
	&-red {
		color: #fff;
		background-color: $red;
		box-shadow: 0px 3px 12px rgba(184, 33, 51, 0.4);
		transition: 0.3s all;
		position: relative;
		overflow: hidden;
		z-index: 1;
		&::after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			height: 100%;
			width: 100%;
			transform: translateX(-100%);
			background-color: rgba(227, 77, 13, 0.5);
			z-index: -1;
			pointer-events: none;
			clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
		}
		&:hover {
			&::after {
				width: 100%;
				animation: hoverBtn 0.3s linear backwards;
			}
		}
	}
	&-black {
		background-color: $black-text;
		transition: 0.3s all;
		&:hover {
			background-color: #fff;
			color: $black-text;
		}
	}

	@include _mobile {
		&-size-1 {
			min-width: 216px;
		}
		&-size-2 {
			min-width: 150px;
		}
		&-size-3 {
			min-width: 200px;
			max-width: 200px;
		}
		&-size-5 {
			min-width: 230px;
			padding: 12.5px 20px;
		}
	}
}

.btn-play {
	text-decoration: none;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	width: fit-content;
	margin-left: auto;
	margin-right: auto;
	cursor: pointer;
	svg {
		width: 70px;
		height: 50px;
		margin-bottom: 10px;
		color: #fff;
	}
	&__caption {
		font-style: normal;
		font-weight: 500;
		font-size: 18px;
		line-height: 21px;
		color: #ffffff;
	}
	&_big {
		svg {
			width: 96px;
			height: 70px;
			margin-bottom: 20px;
		}
	}
	&_small {
		svg {
			width: 46px;
			height: 30px;
		}
	}
	@include _mobile {
		&_big {
			svg {
				width: 55px;
				height: 30px;
				margin-bottom: 0;
			}
		}
	}
}

.btn-details {
	@extend %details-btn-text;
	cursor: pointer;
	text-align: right;
	margin-left: auto;
	width: fit-content;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	text-decoration: none;
	svg {
		width: 16px;
		height: 16px;
		margin-left: 11px;
		transition: 0.3s all;
	}
	&:hover {
		svg {
			transform: translateX(3px);
		}
	}
	&_red {
		color: $red;
	}
	@include _mobile {
	}
}

.btn-like {
	background-color: transparent;
	cursor: pointer;
	border: none;
	width: 28px;
	height: 28px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0;
	svg {
		width: 100%;
		height: 100%;
		color: $black-text;
		fill: transparent;
		transition: 0.3s all;
		// color: $;
	}
	&_active,
	&:hover {
		svg {
			color: $red;
			fill: $red;
		}
	}
}
