.section-uptitle {
	@extend %section-uptitle;
	&_smaller {
		@extend %section-uptitle-2;
		margin-bottom: 4px;
	}
	&-3 {
		@extend %section-uptitle-3;
	}
	&-4 {
		@extend %section-uptitle-4;
	}

	@include _mobile-tablet {
		padding-left: 0;
	}
}
.section-title {
	@extend %section-title;
	&_smaller {
		@extend %section-title-2;
	}
	&-3 {
		@extend %section-title-3;
	}
	&-4 {
		@extend %section-title-4;
	}
	&-5 {
		@extend %section-title-5;
	}
	@include _mobile-tablet {
		padding-left: 0;
	}
}
.section-decoration-title {
	@extend %section-decoration-title;
	position: absolute;
	white-space: nowrap;
	left: 0;
	left: 0;
	top: -60px;
	z-index: -1;
}

.section-title__wrapper {
	@include _mobile {
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 20px;
	}
}

.subtitle-1 {
	@extend %subtitle-1;
}

// h1-h6 titles
@for $i from 1 to 6 {
	h#{$i} {
		@extend %title-#{$i};
	}
}

//titles by class
.title {
	@for $i from 1 to 6 {
		&-#{$i} {
			@extend %title-#{$i};
		}
	}
}
