.blog {
	.pagination {
		margin-top: 30px;
	}

	&-last {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	margin-bottom: 120px;
	@include _mobile {
		margin-bottom: 50px;
	}

	@include _tablet {
		&-last {
			opacity: 1;
			visibility: visible;
			display: block;
		}
	}
}

.blog-card {
	margin-bottom: 40px;
	transition: 0.3s all;
	&__img {
		display: block;
		height: 274px;
		position: relative;
		margin-bottom: 20px;
		z-index: 1;
		border-radius: 9px;
		overflow: hidden;
		transition: inherit;
		&:after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background-color: rgba(44, 44, 44, 0.4);
			z-index: -1;
		}
		img {
			height: 100%;
			position: relative;
			z-index: -1;
		}
	}
	&__date {
		position: absolute;
		padding: 12px 15px;
		@extend %card-text-2;
		background-color: $red;
		border-radius: 0 15px 15px 0;
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: #fff;
		top: 20px;
		left: 0;
		transition: inherit;
	}
	&__title {
		@extend %card-title-4;
		color: #000000;
		margin-bottom: 15px;
		transition: 0.3s all;
		max-height: 42px;
		overflow: hidden;
	}
	&__text {
		@extend %card-text-2;
		color: $grey;
		line-height: 16px;
		font-feature-settings: 'pnum' on, 'lnum' on;
		max-height: 48px;
		min-height: 48px;
		overflow: hidden;
	}
	&__wrapper {
		max-height: 108px;
		min-height: 108px;
		margin-bottom: 20px;
		overflow: hidden;
	}
	&:hover {
		.blog-card__date {
			transform: translateX(-100%);
		}

		.blog-card__title {
			color: $red;
		}
	}
	@include _mobile-tablet {
		&__img {
			height: auto;
			img {
				width: 100%;
			}
		}
	}
	@include _mobile {
		max-width: 420px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 40px;
		&__wrapper {
			margin-bottom: 0;
		}
	}

	@include _laptop-sm {
		&__img {
			height: auto;
			img {
				width: 100%;
			}
		}
	}
	@include _laptop {
		&__img {
			height: auto;
		}
	}
}
