.wishlist {
	margin-bottom: $section-margin_pc;
	.product-card {
		padding: 10px;
		margin-bottom: 40px;
		&__price-uah {
			font-size: 14px;
		}
		&__price-usd {
			font-size: 16px;
		}
		.stickers {
			&__item {
				padding: 8px 15px;
			}
		}
	}

	@include _mobile-tablet {
		.product-card {
			max-width: 420px;
			margin-left: auto;
			margin-right: auto;
			&__img {
				height: auto;
			}
		}
	}
	@include _mobile {
		margin-bottom: $section-margin_mobile;
		.product-card {
			margin-bottom: 20px;
		}
	}
	@media (min-width: 1024px) and (max-width: 1336px) {
		.product-card {
			&__img {
				height: auto;
			}
			&__bottom {
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}
			&__details {
				margin-top: 10px;
			}
		}
	}
}
