.header {
	position: sticky;
	padding-bottom: 9px;
	top: 0;
	z-index: 20;
	transition: 0.3s all;
	&_fixed {
		background-color: #2c2c2c;
	}
	&__top {
		padding: 10px 0;
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}
	&__bottom {
		position: relative;
		z-index: 1;
		display: flex;
		align-items: center;
		justify-content: space-between;
		&::after {
			content: '';
			position: absolute;
			height: calc(100% - 20px);
			border-radius: 25px;
			box-shadow: 0px 4px 4px rgba(184, 33, 51, 0.25);
			background-color: $red;
			width: calc(100% + 80px);
			top: 10px;
			left: -40px;
			z-index: -1;
		}
	}
	&__test-drive {
		@extend %header-text;
		text-transform: uppercase;
		font-weight: bold;
		text-decoration: none;
		transition: 0.3s all;
		display: flex;
		align-items: center;
		justify-content: flex-start;

		&:hover {
			color: $red;
		}
	}
	&__tooltip {
		display: none;
		opacity: 0;
		visibility: hidden;
	}

	&__contacts {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}
	&__contacts-icon {
		width: 20px;
		height: 20px;
		display: flex;
		align-items: center;
		position: relative;
		margin-right: 15px;
		svg {
			width: 100%;
			height: 100%;
			filter: drop-shadow(0px 3px 12px rgba(184, 33, 51, 0.4));
			color: $red;
		}
	}
	&__contacts-text {
		@extend %header-text;
		font-weight: 500;
	}
	&__callback-href {
		@extend %header-callback-href;
		display: block;
		text-decoration: none;
		margin-bottom: 5px;
		transition: 0.3s all;
		&:hover {
			color: $red;
		}
	}
	&__callback-btn {
		@extend %header-callback;
		display: block;
		text-align: right;
		color: $yellow;
		transition: 0.3s all;
		&:hover {
			text-decoration: none;
			color: $red;
		}
	}
	@include _mobile-tablet {
		padding-top: 0;
		height: 70px;
		&_fixed {
			background-color: transparent;
		}
		&__bottom {
			// height: calc(100% - 20px);
			height: 70px;
			&:after {
				width: calc(100% + 30px);
				left: -15px;
				height: 100%;
				// height: calc(100% - 20px);
				border-radius: 0;
				top: 0;
			}
		}
		&__submenu {
			position: relative;
			display: none;
			opacity: 1;
			visibility: visible;
			transition: none;
			width: 100%;
			border-radius: 0;

			&:after {
				content: none;
			}
		}
		&__test-drive {
			padding: 15px 0;
			border-top: 1px solid $border-color;
			border-bottom: 1px solid $border-color;
			width: 100%;
			text-align: center;
			margin-bottom: 20px;
			padding-left: 8px;
		}
		.tooltip {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
		&__contacts {
			margin-bottom: 40px;
			width: 100%;
			justify-content: center;
		}
		&__contacts-icon {
			svg {
				color: #fff;
			}
		}
		&__test-drive:hover {
			color: $black;
		}
		&__callback {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
	}
	@include _mobile {
		&__bottom:after {
			width: calc(100% + 16px);
			left: -8px;
		}

		&__top {
			padding-bottom: 60px;
		}
	}

	@include _laptop-sm {
		&__bottom {
			&::after {
				left: -10px;
				width: calc(100% + 20px);
				height: calc(100% - 10px);
				top: 5px;
			}
		}
		&__logo {
			min-width: 155px;
			height: 64px;
		}
		&__test-drive {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
	}
	@include _laptop {
		&__bottom::after {
			left: -12px;
			width: calc(100% + 24px);
		}
	}
	@media (min-width: 1024px) {
	}
}

.header-catalog {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		position: relative;
		&:last-child {
			.header-submenu {
				left: auto;
				right: 0;
			}
		}
		&:nth-last-child(2) {
			.header-submenu {
				left: auto;
				right: 0;
			}
		}
	}
	&__link {
		display: block;
		padding: 0 22.5px;
		@extend %header-catalog;
		text-decoration: none;
		position: relative;
	}
	&__toggle {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 10px;
		height: 5px;
		position: absolute;
		right: 5px;
		top: 8px;
		transition: 0.3s all;
		svg {
			width: 100%;
			height: 100%;
			color: #fff;
		}
	}
	@include _mobile-tablet {
		justify-content: flex-start;
		width: 100%;
		align-items: flex-start;
		flex-direction: column;
		margin-bottom: 25px;
		&__item {
			flex-wrap: wrap;
			flex-direction: column;
			align-items: flex-start;
			transition: 0.3s all;
			padding: 15px 0;
			width: 100%;
			border-bottom: 1px solid $border-color;
		}
		&__toggle {
			height: 50px;
			width: 40px;
			top: 0;
			right: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			transform: rotate(-90deg);
			svg {
				width: 10px;
				height: 5px;
			}
			&_active {
				transform: rotate(0deg);
			}
		}
		&__link {
			padding-left: 8px;
		}
	}
	@include _laptop-sm {
		flex-wrap: wrap;
		justify-content: flex-end;
		&__link {
			padding-top: 5px;
		}
		&__toggle {
			top: 12px;
		}
	}
	@include _laptop {
		&__toggle {
			top: 6px;
		}
		&__link {
			font-size: 12px;
		}
	}
	@media (min-width: 1024px) and (max-width: 1336px) {
		flex-wrap: wrap;
		justify-content: flex-end;

		&__item:first-child {
			.header-submenu {
				left: 0;
				right: auto;
			}
		}
	}
	@media (min-width: 1024px) {
		&__item:hover {
			.header-catalog__toggle {
				transform: rotate(180deg);
			}
			.header-submenu {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

.header-submenu {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	top: calc(100% + 12px);
	z-index: 2;
	transition: 0.3s all;
	background-color: $red;
	width: 320px;
	border-radius: 0 0 15px 15px;
	overflow: hidden;

	&::after {
		content: '';
		position: absolute;
		width: 100%;
		height: 13px;
		bottom: 100%;
	}
	&__item {
		padding: 15px 30px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-decoration: none;
		transition: 0.3s all;
		&:hover {
			background-color: #9c0e1f;
		}
	}
	&__icon {
		width: 20px;
		min-width: 20px;
		height: 20px;
		margin-right: 15px;
		svg {
			width: 100%;
			height: 100%;
			color: #fff;
		}
	}
	&__title {
		font-weight: 500;
		font-size: 16px;
		line-height: 16px;
		/* identical to box height, or 100% */
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: #ffffff;
	}
	@include _mobile-tablet {
		position: relative;
		display: none;
		opacity: 1;
		visibility: visible;
		transition: none;
		width: 100%;
		border-radius: 0;
		margin-top: 30px;
		&:after {
			content: none;
		}
	}
	@media (min-width: 1024px) and (max-width: 1336px) {
		right: 0;
		left: auto;
		top: 100%;
	}
}

.header-mobile {
	width: 100%;
	position: fixed;
	opacity: 0;
	visibility: hidden;
	top: 70px;
	padding-top: 30px;
	left: 0;
	background-color: $red;
	z-index: -1;
	flex-direction: column;
	transform: translateY(-100%);
	transition: 0.4s all;
	overflow-y: auto;

	.container {
		// flex-direction: column;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		padding: 0;
	}
	&_active {
		transform: translateY(0);
		opacity: 1;
		visibility: visible;
	}

	&__btn {
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	@include _mobile {
		top: 62px;
		height: calc(100% - 62px);
		&__btn {
			margin-right: 0;
		}
		&__actions {
			padding: 0 8px;
			margin-bottom: 10px;
		}
	}
	@include _tablet {
		top: 70px;
		height: calc(100% - 70px);
		padding-top: 50px;
		&__actions {
			padding: 0 15px;
			margin-bottom: 10px;
		}
	}
	@include _mobile-tablet {
		&__btn {
			display: flex;
			justify-content: center;
			align-items: center;
			opacity: 1;
			visibility: visible;
			max-width: 44px;
			padding: 14.5px 0;
			min-width: 44px;
			height: 44px;
			flex-wrap: wrap;
			position: relative;
			z-index: 2;
			cursor: pointer;
			span {
				background-color: #fff;
				height: 3px;
				width: 24px;
				margin-bottom: 3px;
				border-radius: 5px;
				transition: 0.3s all;
				pointer-events: none;
				&:last-child {
					margin-bottom: 0;
				}
			}

			&_active {
				span {
					position: absolute;
					transform-origin: center;
					margin-bottom: 0;
					&:nth-child(1) {
						transform: rotate(45deg);
					}
					&:nth-child(2) {
						transform: translateX(-100%);
						opacity: 0;
					}
					&:nth-child(3) {
						transform: rotate(-45deg);
					}
				}
			}
		}
		&__actions {
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%;
		}
	}
}

.header-search {
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	position: absolute;
	@include _mobile-tablet {
		opacity: 1;
		visibility: visible;
		position: relative;
		pointer-events: inherit;
		.input {
			min-width: 245px;
			input {
				padding: 10px 15px !important;
				background: rgba(255, 255, 255, 0.2);
				border: 1px solid #ffffff;
				border-radius: 15px;
				font-weight: 500;
				font-size: 14px;
				line-height: 16px;
				font-feature-settings: 'pnum' on, 'lnum' on;
				color: rgba(255, 255, 255, 0.8);
				border-radius: 15px !important;
				&::placeholder {
					color: rgba(255, 255, 255, 0.8);
				}
			}
		}
		&__btn {
			position: absolute;
			width: 16px;
			height: 16px;
			padding: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			border: none;
			top: 10px;
			right: 10px;
			cursor: pointer;
			background-color: transparent;
			svg {
				width: 100%;
				height: 100%;
				fill: transparent;
				color: #fff;
			}
		}
	}
	@include _tablet {
		width: 70%;
	}
}
