// lists
%ul-reset {
	padding: 0;
	margin: 0;
	list-style: none;
}
%ul-circle {
	@extend %ul-reset;
	li {
		font-style: normal;
		font-weight: 500;
		font-size: 14px;
		line-height: 16px;
		color: #b3b3b3;
		position: relative;
		padding-left: 23px;
		&::after {
			content: '';
			width: 14px;
			height: 14px;
			border-radius: 50%;
			background-color: $red;
			position: absolute;
			top: 3px;
			left: 0;
		}
	}

	@include _mobile {
		li {
			font-size: 12px;
			line-height: 14px;
			&::after {
				width: 10px;
				height: 10px;
			}
		}
	}
}
%ul-line {
	padding: 0;
	margin: 0;
	list-style-type: '-';
	li {
		font-weight: 500;
		font-size: 16px;
		line-height: 22px;
		/* or 137% */
		color: $black-text;
	}
}
%ul-line-2 {
	@extend %ul-reset;
	li {
		font-weight: normal;
		font-size: 18px;
		line-height: 24px;
		/* or 133% */
		color: #000000;
		&:before {
			content: '-';
		}
	}
	@include _mobile {
		li {
			font-size: 14px;
			line-height: 20px;
		}
	}
}
