.callback {
	position: relative;
	z-index: 1;
	margin-bottom: $section-margin_pc;
	&__decoration-img {
		position: absolute;
		right: 0;
		top: -90px;
		z-index: -1;
		&_left {
			left: -80px;
			right: auto;
			top: -40px;
		}
		&_online-see {
			left: -80px;
			top: auto;
			bottom: -80px;
			z-index: 2;
			width: fit-content;
			img {
				// height: 340px;
			}
		}
		.error {
			opacity: 0.3;
		}
	}
	.section-title_smaller {
		margin-bottom: $section-title-margin_pc;
	}
	.section-uptitle_smaller {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	&__wrapper {
		padding: 65px 100px;
		border-radius: 15px;
		border: 1px solid #2c2c2c6e;
		&_no-border {
			border: none;
			padding: 0;
		}
	}
	&__form {
		max-width: 420px;
		.input {
			margin-bottom: 25px;
			border: 1px solid $red;
			input::placeholder {
				color: #000;
			}
			&::before {
				content: none;
			}
		}
		.btn-primary {
			margin-bottom: 25px;
		}
		&_wide {
			max-width: none;

			.btn-primary {
				margin-bottom: 15px;
			}
		}
	}
	&__form-caption {
		font-family: $secondary-font;
		font-weight: 300;
		font-size: 10px;
		line-height: 12px;
		/* or 120% */
		color: $black-text;
		&_small {
			max-width: 315px;
		}
	}
	&__caption {
		font-weight: 600;
		font-size: 18px;
		line-height: 21px;
		text-transform: uppercase;
		color: $black-text;
		margin-top: 30px;
	}
	@include _mobile {
		&_mobile-margin {
			margin-bottom: 50px;
		}
		&__form {
			width: 100%;
			max-width: none;
			.btn-primary {
				max-width: 300px;
				margin-right: auto;
				margin-left: auto;
			}
		}
		&__wrapper {
			padding: 45px 10px;
		}
		&__decoration-img {
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
		}
		.section-uptitle_smaller {
			text-align: center;
			margin-bottom: 10px;
			justify-content: center;
		}
		.section-title_smaller {
			margin-bottom: $section-title-margin_mobile;
		}
		&__form-caption {
			text-align: center;
		}
		&__caption {
			font-size: 14px;
			line-height: 18px;
			text-align: center;
			margin-bottom: 50px;
		}
		&_online-see {
			.section-title_smaller {
				margin-bottom: 5px;
			}
		}
	}

	@include _tablet {
		&__decoration-img {
			opacity: 0;
		}
		&_online-see {
			.callback__form {
				margin-left: auto;
				margin-right: auto;
			}
		}

		.section-uptitle_smaller,
		.section-title_smaller {
			text-align: center;
			justify-content: center;
		}
		&__form {
			margin-left: auto;
			margin-right: auto;
		}
		&__caption {
			margin-top: 15px;
			margin-bottom: 30px;
		}
	}
	@include _laptop-sm {
		margin-bottom: 70px;
		&__decoraion-img {
			bottom: 50%;
		}

		&_online-see {
			.callback__form {
				margin-left: auto;
				margin-right: 0;
			}
		}
	}

	@media (min-width: 1200px) and (max-width: 1599px) {
		&__decoration-img {
			top: auto;
			bottom: 10%;
			left: -140px;
			img {
				max-width: 60%;
			}
		}
	}
	@media (min-width: 1600px) and (max-width: 1800px) {
		&__decoration-img {
			top: auto;
			bottom: 0;
			img {
				max-width: 68%;
			}
		}
	}
}
