.hero-breadcrumbs {
	position: relative;
	min-height: 320px;
	position: relative;
	top: -134px;
	padding-top: 150px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	// background-attachment: fixed;
	margin-bottom: -64px;
	.container {
		position: relative;
		z-index: 2;
	}
	&__title {
		font-weight: 900;
		font-size: 42px;
		line-height: 49px;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		text-shadow: 0px 1px 4px rgba(184, 33, 51, 0.1);
		-webkit-text-fill-color: transparent;
		-webkit-text-stroke: 2px #fff;
	}
	&:after {
		content: '';
		position: absolute;
		display: block;
		z-index: 1;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba(44, 44, 44, 0.86);
		backdrop-filter: blur(4px);
	}
	@include _mobile {
		margin-bottom: -100px;
		min-height: auto;
		padding-bottom: 30px;
		padding-top: 160px;
		&__title {
			font-size: 20px;
			line-height: 24px;
		}
	}

	@include _tablet {
		margin-bottom: -70px;
		min-height: auto;
		padding-bottom: 30px;
		padding-top: 180px;
		&__title {
			font-size: 32px;
			line-height: 1;
		}
	}
	@include _laptop-sm {
		min-height: auto;
		padding-bottom: 30px;
		&__title {
			font-size: 36px;
			line-height: 40px;
		}
	}

	@include _laptop {
		padding-bottom: 30px;
	}
}
